<template>
  <div :class="customClass">
    <span ref="reference" >
     <slot></slot>
    </span>
    <div :class="[prefixClass + '__container']"
    :style="{top: divPosition.top + 'px', left: divPosition.left + 'px', opacity: opacity, visibility: visibility}"
    ref="popover">
      <div :class="[prefixClass + '__inner']" ref="inner">
        <div :class="[prefixClass + '__title']"
        v-if="title !== ''">
          {{title}}
        </div>
        <div :class="[prefixClass + '__content']">
          <slot name="content"></slot>
        </div>
      </div>
      <div :class="[prefixClass + '__arrow']"
      :style="{top: arrowPosition.top + 'px', left: arrowPosition.left }"></div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-popover'

  export default {
    name: prefixClass,
    props: {
      title: {
        type: String,
        default: ''
      },
      content: {
        type: String,
        default: ''
      },
      placement: {
        type: String,
        default: 'topLeft',  // topLeft | top | topRight
        validator: function (value) {
          return ['top', 'topLeft', 'topRight', 'left', 'leftTop', 'leftBottom', 
                  'right', 'rightTop', 'rightBottom', 'bottom', 'bottomLeft', 'bottomRight'].includes(value)
        }
      },
      trigger: {
        type: String,
        default: 'hover',    // hover | click
        validator: function (value) {
          return ['hover', 'click'].includes(value)
        }
      }
    },
    data () {
      return {
        prefixClass,
        opacity: 0,
        divPosition: {
          top: 0,
          left: 0,
        },
        arrowPosition: {
          top: 0,
          left: '0',
        },
        place: '',
        location: '',
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        customClass += `${prefixClass}__place-${this.place} ` 

        customClass += `${prefixClass}__location-${this.location} ` 
        return customClass
      },
      visibility () {
        return this.opacity === 0 ? 'hidden' : 'visible'
      }
    },
    mounted () {
      this.getLocationAndPlace()
      this.getTriggerMode()
      window.addEventListener('resize',this.computepopoverPosition())
      this.computepopoverPosition();
    },
    methods: {
      changeOpacity () {
        this.opacity = this.opacity === 1 ? 0 : 1
      },
      computePlace (offsetLeft, offsetTop, clientWidth, clientHeight, popoverWidth, innerHeight) {
        let popoverTop = 0
        let popoverLeft = 0
        switch (this.place) {
          case 'top' :
            popoverTop = offsetTop - innerHeight - 7 - 8
            popoverLeft = offsetLeft + (clientWidth / 2)
            break
          case 'bottom' :
            popoverTop = clientHeight + offsetTop + 8 + 5
            popoverLeft = offsetLeft + (clientWidth / 2)
            break
          case 'left' :
            popoverTop = offsetTop + (clientHeight / 2)
            popoverLeft = offsetLeft - (popoverWidth + 5 + 8)
            break
          case 'right' :
            popoverTop = offsetTop + (clientHeight / 2)
            popoverLeft = offsetLeft + clientWidth + 5 + 8
            break
        }

        this.$set(this.divPosition, 'top', popoverTop)
        this.$set(this.divPosition, 'left', popoverLeft)
      },
      computeLocationCenter (innerHeight) {
        let arrowTop = 0
        let arrowLeft = 0
        switch (this.place) {
          case 'top' :
            arrowTop = -7
            arrowLeft = '50%'
            break
          case 'bottom' :
            arrowTop = - innerHeight - 5
            arrowLeft = '50%'
            break
          case 'left' :
            arrowTop = - (innerHeight / 2)
            arrowLeft = '100%'
            break
          case 'right' :
            arrowTop = - (innerHeight / 2)
            break
        }

        this.$set(this.arrowPosition, 'top', arrowTop)
        this.$set(this.arrowPosition, 'left', arrowLeft)
      },
      computeLocationLeft (offsetLeft, popoverWidth, innerHeight) {
        const popoverLeft = offsetLeft + popoverWidth / 2
        const arrowTop = this.place === 'top' ? -7 : (- innerHeight - 5)
        const arrowLeft = '16px'

        this.$set(this.divPosition, 'left', popoverLeft)
        this.$set(this.arrowPosition, 'top', arrowTop)
        this.$set(this.arrowPosition, 'left', arrowLeft)
      },
      computeLocationRight (offsetLeft, clientWidth, popoverWidth, innerHeight) {
        const popoverLeft = offsetLeft + clientWidth - popoverWidth / 2
        const arrowTop = this.place === 'top' ? -7 : (- innerHeight - 5)
        const arrowLeft = (popoverWidth - 16 - 10) + 'px'

        this.$set(this.divPosition, 'left', popoverLeft)
        this.$set(this.arrowPosition, 'top', arrowTop)
        this.$set(this.arrowPosition, 'left', arrowLeft)
      },
      computeLocationTop (offsetTop, innerHeight) {
        const popoverTop = offsetTop 
        const arrowTop = - innerHeight + 16
        const arrowLeft = this.place === 'left' ? '100%' : 0

        this.$set(this.divPosition, 'top', popoverTop)
        this.$set(this.arrowPosition, 'top', arrowTop)
        this.$set(this.arrowPosition, 'left', arrowLeft)
      },
      computeLocationBottom (offsetTop, clientHeight, innerHeight) {
        const popoverTop = offsetTop + clientHeight - innerHeight
        const arrowTop = - 16 - 10 - 10
        const arrowLeft = this.place === 'left' ? '100%' : 0

        this.$set(this.divPosition, 'top', popoverTop)
        this.$set(this.arrowPosition, 'top', arrowTop)
        this.$set(this.arrowPosition, 'left', arrowLeft)
      },
      computepopoverPosition () {
        const reference = this.$refs.reference.children[0]
        const popover = this.$refs.popover
        const inner = this.$refs.inner
        const offsetLeft = reference.offsetLeft
        const offsetTop = reference.offsetTop
        const clientWidth = reference.clientWidth + 2
        const clientHeight = reference.clientHeight + 2
        const popoverWidth = popover.clientWidth
        const innerHeight = inner.clientHeight

        this.computePlace (offsetLeft, offsetTop, clientWidth, clientHeight, popoverWidth, innerHeight)
        switch (this.location) {
          case 'center' :
            this.computeLocationCenter(innerHeight)
            break
          case 'top' :
            this.computeLocationTop(offsetTop, innerHeight)
            break
          case 'left' :
            this.computeLocationLeft(offsetLeft, popoverWidth, innerHeight)
            break
          case 'bottom' :
            this.computeLocationBottom(offsetTop, clientHeight, innerHeight)
            break
          case 'right' :
            this.computeLocationRight(offsetLeft, clientWidth, popoverWidth, innerHeight)
            break
        }
      },
      getLocationAndPlace () {
        const stringArray = this.placement.split('')
        let index = -1
        stringArray.forEach(t => {
          if(t >= 'A' && t <= 'Z') {
            index = stringArray.indexOf(t)
          }
        })
        
        if(index > -1) {
          this.place = this.placement.slice(0,index)
          this.location = this.placement.substring(index).toLowerCase()
        } else {
          this.place = this.placement
          this.location = 'center'
        }
      },
      getTriggerMode () {
        const reference = this.$refs.reference
        if (this.trigger === 'click') {
          reference.addEventListener('click',this.changeOpacity)
          return
        }

        reference.addEventListener('mouseenter',this.changeOpacity)
        reference.addEventListener('mouseleave',this.changeOpacity)
      }
    }
  }
</script>