<template>
  <div :class="customClass">
    <div v-if="isActive">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-tab'

  export default {
    name: prefixClass,
    props: {
      label: { 
        type: String, 
        required: true 
      },
      id: { 
        type: [String, Number], 
        required: true 
      },
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      },
      isActive() {
        return this.cdTabs.activeId === this.id;
      }
    },
    inject: ["cdTabs"],
    created() {
      this.cdTabs.childrens.push(this);
    },
    method: {
    }
  }
</script>