<template>
  <div :class="customClass"
  :style="{paddingLeft: paddingLeft}">
    <div :class="contentClass" @click="handleClick"> 
      <i :class="[`${prefix}-icon `, `${prefix}-icon-sanjiaoxing-xia`]"></i>
      <checkbox 
      v-if="select" 
      ref="checkbox"
      :value="dataList.label"
      :checked="dataList.isChecked"
      @change="handleChecked(dataList)"
      ></checkbox>

      <span :class="`${prefixClass}__label`" >{{dataList.label}}</span>
    </div>

    <div 
    :class="`${prefixClass}__group`"
    v-if="havaChildren(dataList)" v-show="open">
      <cd-tree-item
      v-for="(item, index) in dataList.children" :dataList="item"
      :key="index"
      :level="item.level"
      :select="select"></cd-tree-item>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import Checkbox from '../checkbox'

  const prefixClass = prefix + '-tree-item'

  export default {
    name: prefixClass,
    components: {
      'checkbox': Checkbox
    },
    props: {
    
      dataList: {
        type: [Object, Array],
        required: true
      },
      level: {
        type: [Number, String],
        default: 1
      },
      select: {
        type: Boolean,
        default: false
      },
    },
    data () {
      return {
        prefix,
        prefixClass,
        open: false,
        expand: false,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        customClass += `${prefixClass}__${this.level} `

        if (this.dataList.isLeaf) {
          customClass += `${prefixClass}__leaf `
        }
      
        return customClass
      },
      contentClass () {
        let contentClass = `${prefixClass}__content `

        if (this.open) {
          contentClass += `${prefixClass}__expand `
        }
        
        return contentClass
      },
      paddingLeft () {
        let multiple = 0
        multiple = Number(this.level) === 3 ? 0 : (this.level - 1)
        return 26 * multiple + 'px'
      }
    },
    mounted() {
    },
    methods: {
      havaChildren (data) {
        return data.children && (data.children.length > 0)
      },
      handleClick () {
        this.open = !this.open
      },

      handleChecked (data) {
        data.isChecked = !data.isChecked
        if (!data.isLeaf) {
          data.children.forEach(item => {
            item.isChecked = data.isChecked
            
            if(this.havaChildren(item)) {
              item.children.forEach(itemChild => {
                itemChild.isChecked = data.isChecked
              })
            }

          });

        }
      },

  }
}
</script>