<template>
  <div :class="customClass">
    <div v-if="theme === 'operation'" :class="`${prefixClass}__container`">
        <i :class="iconClass"></i>
      <div :class="`${prefixClass}__title`">{{title}}</div>
      <div :class="`${prefixClass}__btnList`">
        <slot name="btnList"></slot>
      </div>
    </div>


    <div v-else :class="`${prefixClass}__container`">
      <div :class="`${prefixClass}__image`">
        <img :src="srcList[0]" :srcset="srcList[1]" alt="">
        <div :class="`${prefixClass}__title`">{{title}}</div>
      </div>
      <div :class="`${prefixClass}__description`">
        <slot name="description"></slot>
      </div>
      <div :class="`${prefixClass}__btnList`">
        <slot name="btnList"></slot>
      </div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-result'

  export default {
    name: prefixClass,
    props: {
      type: {
        type: String,
        default: 'success',
        validator: function (value) {
          return ['success', 'info', 'warning', '404', '403', '500'].includes(value)
        }
      },
      title: {
        type: String,
        default: '',
      },
      description: {
        type: String,
        default: '',
      },
      imgSrc: {
        type: String,
        default: '',
      },
      imgSrcset: {
        type: String,
        default: '',
      }
    },
    data () {
      return {
        prefixClass,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        customClass += `${prefixClass}__${this.type} `

        customClass += `${prefixClass}__${this.theme} `

        return customClass
      },
      theme () {
        return ['success', 'info', 'warning'].includes(this.type) ? 'operation' : 'pageError'
      },
      iconClass () {
        let iconClass =  'cds-icon '

        if (this.theme === 'pageError') return

        switch (this.type) {
          case 'success':
            iconClass += `cds-icon-chenggongtishi `
            break
          case 'info':
            iconClass += `cds-icon-xiaoxitishi `
            break
          case 'warning':
            iconClass += `cds-icon-jingshitishi `
            break
        }

        return iconClass
      },
      srcList () {
        let src = ''
        let srcset = ''
        if (this.theme === 'operation') return

        if (this.imgSrc !== '' && this.imgSrcset === '') {
          src = this.imgSrc
          return [src,srcset]
        }

        src = this.imgSrc === '' ? require(`../../assets/page${this.type}.png`) : this.imgSrc
        srcset = this.imgSrcset === '' ? require(`../../assets/page${this.type}@2x.png`) : this.imgSrcset
 
        return [src,srcset]
      }
    },
    methods: {
    }
  }
</script>