<template>
  <transition name="fade">
    <div :class="customClass" v-if="isShow">
      <i :class="iconClass"></i>
      <div :class="`${prefixClass}__content`">
        <div :class="`${prefixClass}__title`">{{title}}</div>
        <div :class="`${prefixClass}__description`">{{description}}</div>
      </div>
      
      <a 
      :href="link"
      v-if="link !== ''" 
      :class="`${prefixClass}__link`">
      </a>
      <i class="cds-icon cds-icon-guanbi" @click="close" v-else></i>
      
    </div>
  </transition>
</template>


<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-alert'

  export default {
    name: prefixClass,
    props: {
    type: {
      type: String,
      default: 'info',
      validator(value) {
        return ['success', 'info', 'warning', 'error'].includes(value);
      },
    },
    title: {
      type: String,
      default: '',
    },
    description: {
      type: String,
      default: '',
    },
    link: {
      type: String,
      default: '',
    }
  },
    data () {
      return {
        prefixClass,
        isShow: true,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        customClass += `${prefixClass}__${this.type} ` 

        if (this.description !== '') {
          customClass += `${prefixClass}__subsidiary ` 
        }
        
        return customClass
      },
      iconClass () {
        let iconClass =  'cds-icon '
        console.log(this.type)
        if (this.description === '') {
          switch (this.type) {
            case 'success':
              iconClass += `cds-icon-chenggongtishi ` 
              break
            case 'info':
              iconClass += `cds-icon-xiaoxitishi ` 
              break
            case 'warning':
              iconClass += `cds-icon-jingshitishi ` 
              break
            case 'error':
              iconClass += `cds-icon-cuowu ` 
              break
          }
          return iconClass
        } 
        switch (this.type) {
          case 'success':
            iconClass += `cds-icon-yichenggong ` 
            break
          case 'info':
            iconClass += `cds-icon-bangzhu ` 
            break
          case 'warning':
            iconClass += `cds-icon-qingzhuyi ` 
            break
          case 'error':
            iconClass += `cds-icon-chucuo ` 
            break
        }
        
        return iconClass
      },
    },
    methods: {
      close(e) {
        this.isShow = false;
        this.$emit('close', e);
      }
    }
  }
</script>