<template>
  <transition name="message-fade" @after-leave="destroy">
    <div 
    :class="customClass"
    :style="{ top: offset + 'px', zIndex: zIndex}"
    v-show="visible"
    @mouseenter="clearTimer"
    @mouseleave="createTimer"
    >
      <div :class="`${prefixClass}__container`">
        <i :class="iconClass"></i>
        <span :class="`${prefixClass}__content`">{{ content }}</span>
        <i class="cds-icon cds-icon-guanbi" @click.stop.prevent="close" v-if="closable"></i>
      </div>
    </div>
  </transition>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-message'

  export default {
    name: prefixClass,
    data () {
      return {
        prefixClass,
        visible: false,
        type: 'info',   // info | success | warning | error
        content: '',
        closed: false,
        offset: 16,
        zIndex: 1000,
        duration: 3000,
        onClose: null,
        timer: null,
        closable: false,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        customClass += `${prefixClass}__${this.type} ` 

        return customClass
      },
      iconClass () {
        let iconClass =  'cds-icon '
        
          switch (this.type) {
            case 'success':
              iconClass += `cds-icon-chenggongtishi ` 
              break
            case 'info':
              iconClass += `cds-icon-xiaoxitishi ` 
              break
            case 'warning':
              iconClass += `cds-icon-jingshitishi ` 
              break
            case 'error':
              iconClass += `cds-icon-cuowu ` 
              break
          }
        
        return iconClass
      }
    },
    watch: {
      closed(newVal) {
        if (newVal) {
          this.visible = false;
        }
      }
    },
    mounted() {
      this.createTimer()
    },
    methods: {
      createTimer () {
        if (this.duration > 0) {
          this.timer = setTimeout(() => {
            if (!this.closed) {
              this.close();
            }
          }, this.duration)
        }
      },
      clearTimer() {
        if (this.timer) {
          clearTimeout(this.timer);
        }
      },
      close() {
        this.closed = true;
        if (typeof this.onClose === 'function') {
          this.onClose();
        }
        this.visible = false
      },
      destroy() {
        document.body.removeChild(this.$el)
        this.$destroy()
      }
    },
    beforeDestroy () {
      this.clearTimer()
    }
  }
</script>