<template>
  <div :class="customClass">
    <table ref="table" style="width: 100%;">
      <colgroup>
        <col v-for="(column, index) in columns" 
          :name="column.prop"
          :width="column.width">
      </colgroup>
      <thead>
        <tr>
          <th v-for="column in columns"
            align="left"
            colspan="1"
            rowspan="1"
            :class="[column.prop]">
            <div :class="
              [prefix + '-table__th', 
              { 'is-checkbox': column.type === 'checkbox' }
            ]">
              <cd-checkbox v-if="column.type === 'checkbox'" @change="$table.toggleCheckAll"></cd-checkbox>
              <template v-else>
                <span style="margin-right: 4px;">{{ column.label }}</span>
                <template v-if="column.sortable">
                  <i 
                    :class="[
                      'cds-icon cds-icon-sanjiaoxing-xia',
                      'sortable-icon__asc',
                      { 'active': getCurrentSortableActive(column.prop, 'asc') }
                    ]"
                    @click="sort(column.prop, 'asc')"
                  ></i>
                  <i 
                    :class="[
                      'cds-icon cds-icon-sanjiaoxing-xia',
                      'sortable-icon__desc',
                      {'active': getCurrentSortableActive(column.prop, 'desc')}
                    ]"
                    @click="sort(column.prop, 'desc')"
                  ></i>
                </template>
              </template>
            </div>
          </th>
        </tr>
      </thead>
    </table>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import CdCheckbox from '../checkbox'

  const prefixClass = prefix + '-table__header'

  export default {
    name: prefixClass,
    components: {
      CdCheckbox
    },
    props: {
      columns: Array,
    },
    data () {
      return {
        prefix,
        prefixClass,
        $table: null
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    created() {
      this.$table = this.getParentTableVnode()
    },
    methods: {
      getParentTableVnode () {
        let $parent = this.$parent
        let index = 0

        while (index++ < 100) {
          if (!$parent) {
            return {}
          } else if ($parent.$options.name === `${prefix}-table`) {
            return $parent
          } else {
            $parent = $parent.$parent
          }
        }
      },
      sort (prop, type) {
        this.$set(this.$table.sortable, 'prop', prop)
        this.$set(this.$table.sortable, 'type', type)
      },
      getCurrentSortableActive (prop, type) {
        const sortable = this.$table.sortable

        return sortable.prop === prop && sortable.type === type
      },
    }
  }
</script>