<template>
  <div :class="customClass">
    <div :class="`${prefixClass}__outsideContainer`">
      <div :class="`${prefixClass}__image`">
        <img :src="srcList[0]" :srcset="srcList[1]" alt="">
      </div>
      <div :class="`${prefixClass}__container`">
        <div :class="`${prefixClass}__title`">{{title}}</div>
        <div :class="`${prefixClass}__btnList`">
          <slot name="btnList"></slot>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-blank-page'

  export default {
    name: prefixClass,
    props: {
      type: {
        type: String,
        default: 'internet',
        validator: function (value) {
          return ['internet', 'info', 'search'].includes(value)
        }
      },
      title: {
        type: String,
        default: '',
      },
      imgSrc: {
        type: String,
        default: '',
      },
      imgSrcset: {
        type: String,
        default: '',
      }
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        return customClass
      },
      srcList () {
        let src = ''
        let srcset = ''

        if (this.imgSrc !== '' && this.imgSrcset === '') {
          src = this.imgSrc
          return [src,srcset]
        }

        src = this.imgSrc === '' ? require(`../../assets/blankPage-${this.type}.png`) : this.imgSrc
        srcset = this.imgSrcset === '' ? require(`../../assets/blankPage-${this.type}@2x.png`) : this.imgSrcset
 
        return [src,srcset]
      }
    },
    methods: {
    }
  }
</script>