<template>
  <div :class="customClass">
    <img v-if="img" :src="img" :class="[prefixClass + '__img']" />
    <i v-else-if="icon" :class="[prefixClass + '__icon', icon]"></i>
    <span v-else-if="text" :class="[prefixClass + '__text']">{{ text }}</span>
    <i v-else :class="[prefixClass + '__default', 'cds-icon cds-icon-touxiang']"></i>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-avatar'

  export default {
    name: prefixClass,
    props: {
      shape: {
        type: String,
        default: 'round', // round | square
      },
      size: {
        type: String,
        default: 'medium', // large | medium | mini
      },
      img: String,
      icon: String,
      text: String,
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.shape === 'round') {
          customClass += `${prefixClass}__round `
        } else if (this.shape === 'square') {
          customClass += `${prefixClass}__square `
        }

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break
          case 'mini':
            customClass += `${prefixClass}__mini `
            break
        }

        if (this.src) {
          customClass += 'is-img '
        } else if (this.icon) {
          customClass += 'is-icon '
        } else if (this.text) {
          customClass += 'is-text '
        }
        
        return customClass
      }
    },
    methods: {
    }
  }
</script>