<template>
  <div :class="customClass">
    <div :class="[ `${prefix}-tabs__header` ]">
      <div
        :class="[`${prefix}-tabs__header-item`,{ [`${prefix}-tabs__header-item__active`]: activeId === item.id }]"
        v-for="(item, index) in childrens"
        :key="index"
        @click.stop="handleClick(item)"
        
      >
        <span>{{ item.label }}</span>
      </div>
    </div>

    <div :class="[`${prefix}-tabs__content`]">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-tabs'

  export default {
    name: prefixClass,
    props: {
      value: { 
        type: [Number, String] 
      },
      type: { 
        type: String, 
        default: 'default' 
      }
    },
    data() {
      return {
        prefix,
        prefixClass,
        activeId: "",
        childrens: []                               
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        switch(this.type) {
          case 'default':
            customClass += `${prefixClass}__default `
            break
          case 'card':
            customClass += `${prefixClass}__card `
            break
          case 'block':
            customClass += `${prefixClass}__block `
            break
          case 'edge':
            customClass += `${prefixClass}__edge `
            break
          case 'hover':
            customClass += `${prefixClass}__hover `
            break
        }
        return customClass
      },
    
    },
    provide() {
      return {
        cdTabs: this
      };
    },
    watch: {
      childrens(value) {
        if (this.childrens.length) {
          for (const item of this.childrens) {
            if (this.value === item.id) {
              this.activeId = item.id;
              return;
            }
          }
          this.activeId = this.childrens[0].id;
        }
      },
      value(value) {
        this.activeId = value;
      }
    },
    methods: {
      handleClick(item) {
        if (this.activeId !== item.id) {
          this.activeId = item.id;
          this.$emit("input", item.id);
        }
        this.$emit('click',item.id)
      }
    }
  }
</script>