<template>
  <div :class="customClass">
    <div :class="[prefixClass + '__panel']" @click="toggle">
      <i class="cds-icon cds-icon-top" :class="[{'is-bottom': isCollapse}]"></i>
      {{ title }}
    </div>
    <div :class="[prefixClass + '__content', {'hidden': isCollapse}]"
      ref="content"
      :style="{ height: clientHeight + 'px' }">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-collapse'

  export default {
    name: prefixClass,
    props: {
      type: String,
      value: Boolean,
      title: {
        type: String,
        required: true
      },
      'noBorder': {
        type: Boolean,
        default: false,
      }
    },
    data () {
      return {
        prefixClass,
        clientHeight: 0,
        isCollapse: this.value, // true =  false = 
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.noBorder) {
          customClass += 'no-border '
        }
        
        return customClass
      }
    },
    mounted () {
      this.calcClientHeight()
    },
    methods: {
      toggle () {
        this.isCollapse = !this.isCollapse
        this.$emit('input', this.isCollapse)
        
        if (!this.isCollapse) {
          this.calcClientHeight()
        }
      },
      getNumber (str) {
        const number = parseFloat(str)

        if (isNaN(number)) {
          return 0
        }

        return Number(number)
      },
      calcClientHeight () {
        const style = window.getComputedStyle(this.$slots.default[0].elm)
        const marginTop = this.getNumber(style.marginTop)
        const marginBottom = this.getNumber(style.marginBottom)
        const height = this.getNumber(style.height)

        this.clientHeight = marginTop + marginBottom + height
      }
    }
  }
</script>