<template>
  <div :class="customClass">
    <div v-for="item in list"
      :class="[prefixClass + '__item']">
      <div :class="[prefixClass + '__point', item.status]"></div>
      <div :class="[prefixClass + '__content']" v-html="item.content">
      </div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-timeline'

  export default {
    name: prefixClass,
    props: {
      list: {
        type: Array,
        default: function() {
          return []
        }
      }
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
    }
  }
</script>