<template>
  <div :class="[customClass, `${customClass}--${listType}`]" v-if="listType === 'file'">
    <div class="file-box">
      <p v-if="files.length <= 0"  class="placeholder"></p>
      <ul v-else>
        <li
            v-for="file in files"
            :class="[{
              'is-success': file.status === 'success',
              'is-fail': file.status === 'fail',
              'is-uploading': file.status === 'uploading',
              }]"
        >
          <span class="name">{{ file.name }}</span>
          <span class="size">({{ (file.size / 1024 / 1024).toFixed(2) }}MB)</span>
          <i @click="onRemove(file)" class="cds-icon cds-icon-guanbi"></i>
        </li>
      </ul>
      <label class="add-btn">
        <i class="cds-icon cds-icon-tianjia"></i>
      </label>
    </div>
  </div>
  <ul v-else :class="[customClass, `${customClass}--${listType}`]">
    <li
        v-for="file in files"
        :class="[{
            'is-success': file.status === 'success',
            'is-fail': file.status === 'fail',
            'is-uploading': file.status === 'uploading',
          }]"
    >
      <template v-if="listType === 'text'">
        <i class="cds-icon cds-icon-qingchu"></i>
        <div class="info">
          <div class="top">
            <p class="name">{{ file.name }}</p>
            <i v-show="file.status === 'fail'" @click="onReupload(file)" class="cds-icon cds-icon-huanyuan"></i>
            <i v-show="['fail', 'uploading'].indexOf(file.status) > -1" @click="onRemove(file)"
               class="cds-icon cds-icon-guanbi"></i>
          </div>
          <div class="progress">
            <div class="finished" :style="{ width: `${file.percentage}%` }"></div>
          </div>
        </div>
      </template>
      <template v-else-if="listType === 'picture-card'">
        <i @click="onRemove(file)" class="cds-icon cds-icon-cuowu"></i>
        <img :src="file.url" alt="">
        <div v-if="file.status === 'fail'" class="mask">
          <i class="cds-icon cds-icon-jinggao unhover"></i>
          <span class="text unhover"></span>
          <i @click="onReupload(file)" class="cds-icon cds-icon-huanyuan hover"></i>
          <span class="text hover"></span>
        </div>
        <div v-if="file.status === 'uploading'" class="mask">
          <span class="text"></span>
          <div class="progress">
            <div class="finished" :style="{ width: `${file.percentage}%` }"></div>
          </div>
        </div>
      </template>
    </li>
  </ul>
</template>

<script>
import {prefix} from '../../utils'

const prefixClass = prefix + '-upload-list'

export default {
  name: prefixClass,
  props: {
    files: {
      type: Array,
      defualt() {
        return []
      },
    },
    onRemove: Function,
    onReupload: Function,
    listType: String
  },
  data() {
    return {
      prefixClass
    };
  },
  computed: {
    customClass() {
      let customClass = prefixClass

      return customClass
    }
  },
  mounted() {
  },
  methods: {}
}
</script>