<template>
	<button 
		:class="customClass" 
		@click="handleClick"
	>
		<div v-if="loading && type !== 'text'" class="cds-icon cds-icon-loading"></div>
		<slot></slot>
	</button>
</template>

<script>
	import { prefix } from '../../utils';
	
	const prefixClass = `${prefix}-button`;

	export default {
		name: prefixClass,
		props: {
			type: {
				type: String,
				default: 'default'
			},
			size: {
				type: String,
				default: 'medium'
			},
			icon: { 
				type: String,
			},
			ghost: {
				type: Boolean,
				default: false,
			},
			disabled: {
				type: Boolean,
				default: false,
			},
			loading: {
				type: Boolean,
				default: false,
			}
		},
		computed: {
			customClass() {
				let customClass = prefixClass + ' '

				switch (this.type) {
					case 'default':
						customClass += `${prefixClass}__default `
						break;
					case 'primary':
						customClass += `${prefixClass}__primary `
						break;
					case 'danger':
						customClass += `${prefixClass}__danger `
						break;
					case 'text':
						customClass += `${prefixClass}__text `
						break;
				}

				if (this.type !== 'text') {
					switch (this.size) {
						case 'large':
							customClass += `${prefixClass}__large `
							break;
						case 'medium':
							customClass += `${prefixClass}__medium `
							break;
						case 'mini':
							customClass += `${prefixClass}__mini `
							break;
					}
				}

				if (this.disabled) {
					customClass += `${prefixClass}__disabled `
				}

				if (this.loading) {
					customClass += `${prefixClass}__loading `
				}

				if (this.ghost) {
					customClass += `${prefixClass}__ghost `
				}

				return customClass;
			}
		},
		methods: {
			handleClick(e) {
				if (this.disabled) {
					e.preventDefault();
					e.stopPropagation();

					return;
				}
				this.$emit('click', e)
			}
		}
	}
</script>
