<template>
  <div
      :class="customClass"
      @drop.prevent="onDrop"
      @dragover.prevent="onDragover"
  >
    <slot></slot>
  </div>
</template>

<script>
import {prefix} from '../../utils'

const prefixClass = prefix + '-upload-dragger'

export default {
  name: prefixClass,
  props: {},
  data() {
    return {
      prefixClass
    };
  },
  computed: {
    customClass() {
      let customClass = prefixClass + ' '

      return customClass
    }
  },
  methods: {
    onDrop(e) {
      console.log('%c ========>>> upload-dragger.vue onDrop <<<========', 'background-color: #36964d; color: #fff;')
      this.$emit('file', e.dataTransfer.files)
    },
    onDragover() {

    }
  }
}
</script>