<template>
  <div :class="customClass" ref="timePicker">
    <input type="text" 
      :value="innerValue" 
      @change="handleChange"
      @focus="handleFocus"
      @blur="handleBlur"
      :disabled="disabled"
      readonly />
    <i class="cds-icon cds-icon-shijian"></i>
    <div v-show="isShowTimePanel" :class="prefixClass + '__panel'">
      <cd-time v-model="innerValue" @input="handleChange" />
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  import CdTime from '../components/time.vue'

  const name = prefix + 'TimePicker'
  const prefixClass = prefix + '-time-picker'

  export default {
    name,
    components: {
      CdTime
    },
    props: {
      value: {
        type: String,
        default: ''
      },
      size: {
        type: String,
        default: 'medium'
      },
      disabled: {
        type: Boolean,
        default: false,
      }
    },
    data () {
      return {
        prefixClass,
        isShowTimePanel: false,
        innerValue: this.value
      };
    },
    created() {
      window.addEventListener('click', this.hideTimePanel.bind(this))
    },
    beforeDestroy() {
      window.removeEventListener('click', this.hideTimePanel.bind(this))
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break
          case 'mini':
            customClass += `${prefixClass}__mini `
            break
        }

        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }
 
        return customClass
      },
    },
    methods: {
      handleChange() {
        this.$emit('input', this.innerValue)
      },
      handleFocus(event) {
        this.showTimePanel()
        this.$emit('focus', event)
      },
      handleBlur(event) {
        this.$emit('blur', event)
      },
      showTimePanel() {
        this.isShowTimePanel = true
      },
      hideTimePanel(event) {
        if (this.validateChild(event.target)) return

        this.isShowTimePanel = false
      },
      validateChild(dom) {
        const parentNode = dom.parentNode

        if (!parentNode) {
          return false
        }

        if (parentNode === this.$refs.timePicker) {
          return true
        } else {
          return this.validateChild(parentNode)
        }
      }
    }
  }
</script>