<template>
  <transition name="notification-fade" @after-leave="destroy">
    <div 
    :class="customClass"
    :style="positionStyle"
    v-show="visible"
    @mouseenter="clearTimer" 
    @mouseleave="createTimer">

      <div :class="containerClass">
        <i :class="iconClass" v-if="type !== ''"></i>
        <div :class="`${prefixClass}__content`">
          <div :class="`${prefixClass}__title`">{{ title }}</div>
          <div :class="`${prefixClass}__description`" v-if="description !== ''">{{ description }}</div>
        </div>
        <i class="cds-icon cds-icon-guanbi" @click.stop.prevent="close"></i>

      </div>
    </div>
  </transition>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-notification'

  export default {
    name: prefixClass,
    data () {
      return {
        prefixClass,
        visible: false,
        type: '',   // info | success | warning | error
        title: '',
        description: '',
        closed: false,
        offset: 16,
        zIndex: 1000,
        duration: 3000,
        onClose: null,
        timer: null,
        position: 'topRight',   // topLeft | topRight | bottomLeft | bottomRight
        horizontal: '',
        vertical: '',
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.type !== '') {
          customClass += `${prefixClass}__${this.type} ` 
        }

        customClass += `${prefixClass}__${this.horizontal} `

        return customClass
      },
      iconClass () {
        let iconClass =  'cds-icon '
        
          switch (this.type) {
            case 'success':
              iconClass += `cds-icon-yichenggong ` 
              break
            case 'info':
              iconClass += `cds-icon-bangzhu ` 
              break
            case 'warning':
              iconClass += `cds-icon-qingzhuyi ` 
              break
            case 'error':
              iconClass += `cds-icon-chucuo ` 
              break
          }
        
        return iconClass
      },
      containerClass () {
        let containerClass = `${prefixClass}__container `

        if (this.description === '') {
          containerClass += `${prefixClass}__onlyTitle ` 
        }

        return containerClass
      },
      positionStyle () {
        let style = { zIndex: this.zIndex}

        style[this.horizontal] = '0'
        style[this.vertical] = `${this.offset}px`

        return style
      }
    },
    watch: {
      closed(newVal) {
        if (newVal) {
          this.visible = false;
        }
      }
    },
    mounted() {
      this.getPosition()
      this.createTimer()
    },
    methods: {
      createTimer () {
        if (this.duration > 0) {
          this.timer = setTimeout(() => {
            if (!this.closed) {
              this.close();
            }
          }, this.duration)
        }
      },
      clearTimer () {
        if (this.timer) {
          clearTimeout(this.timer)
        }
      },
      close() {
        this.closed = true;
        if (typeof this.onClose === 'function') {
          this.onClose();
        }
        this.visible = false
      },
      getPosition () {
        const stringArray = this.position.split('')
        let index = -1
        stringArray.forEach(t => {
          if(t >= 'A' && t <= 'Z') {
            index = stringArray.indexOf(t)
          }
        })
        
        if(index > -1) {
          this.vertical = this.position.slice(0,index)
          this.horizontal = this.position.substring(index).toLowerCase()
        } 
      },
      destroy() {
        document.body.removeChild(this.$el)
        this.$destroy()
      }
    },
    beforeDestroy () {
      this.clearTimer()
    }
  }
</script>