<template>
  <div :class="customClass" @click="handleClick">
      <div :class="[`${prefix}-radio__circle `]"></div>
      <input type="radio" :value="label" :disabled="disabled" >
      <div :class="[`${prefix}-radio__check `]" v-if="isChecked"></div>
      <span><slot></slot></span>  
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  const prefixClass = prefix + '-radio'

  export default {
    name: prefixClass,
    props: {
      label: {
        type: [String,Number,Boolean],
        required: true,
      },
      disabled: {
				type: Boolean,
				default: false,
      },
      value: {
				type: [String,Number,Boolean],
				default: "",
      },
    },
    data () {
      return {
        prefix,
        prefixClass,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if (this.isChecked) {
          customClass += `${prefixClass}__checked `
        }

        if (this.isDisabled) {
          customClass += `${prefixClass}__disabled `
        }

        return customClass
      },
      isGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.name !== `${prefix}-radio-group`) {
            parent = parent.$parent;
          }
          else {
            return true;
          }
        }
        return false;
      },
      isDisabled() {
        return this.$parent.disabled || this.disabled;
      },
      isChecked() {
        if ( this.isGroup && (this.$parent.value === this.label) ) {
          return true;
        }
        else if( !this.isGroup && (this.value === this.label) ) {
          return true;
        }
        return false
      }
    },
    methods: {
      handleClick(e) {
        if (this.isDisabled) {
          e.preventDefault();
          e.stopPropagation();
          return;
        }

        if(this.isGroup) {
          this.$parent.$emit('input', this.label)
          this.$parent.$emit('change', this.label)
        }

        this.$emit("input", this.label);
        this.$emit('change', this.label)
      }
    }
  }
</script>