<template>
  <span :class="customClass" @click="handleClick">
    <span :class="[`${prefix}-breadcrumb-item__title`]" ref="link">
        <slot></slot>
    </span>
    <span :class="[`${prefix}-breadcrumb-item__separator`]">/</span>
  </span>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-breadcrumb-item'
  export default {
    name: prefixClass,
    props: {
      to: {
        type: [String,Object],
        default: ''
      }
    },
    data () {
      return {
        prefix,
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if(!this.to && !this.haveHref) {
          customClass += `${prefixClass}__link `
        }
        
        return customClass
      },
      haveHref () {
        for(const item of this.$slots.default) {
          if(item.tag === 'a') {
            return true
          }
        }
        return false
      },
    },
    methods: {
      handleClick () {    
          this.$router.push(this.to);
      }
    }
  }
</script>