<template>
  <div :class="customClass"> 
    <slot></slot>
  </div>
</template>

<script>
import { prefix } from '../../utils';
const prefixClass = `${prefix}-steps`;

export default {
  name: prefixClass,
  props: {
    status: {
      validator (value) {
        return ['wait', 'process', 'finish', 'error'].includes(value);
      },
      default: 'process'
    },
    active: {
      type: Number,
      default: 0
    },
    direction: {
      type: String,
      default: 'horizontal'
    },
    size: {
      type: String,
      default: 'default'
    },
    type: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      prefixClass,
      steps: []
    }
  },
  watch: {
    steps(steps) {
      steps.forEach((child, index) => {
        child.index = index;
      })
    }
  },
  computed: {
    customClass () {
      let customClass = prefixClass + ' '
      if( this.type !== 'simple' ) {
        switch(this.direction) {
          case 'horizontal':
            customClass += `${prefixClass}__horizontal `
            break
          case 'vertical':
            customClass += `${prefixClass}__vertical `
            break
        }

        switch(this.size) {
          case 'default':
            customClass += `${prefixClass}__default `
            break
          case 'small':
            customClass += `${prefixClass}__small `
            break
        }
      }
      else {
        customClass += `${prefixClass}__simple `
      }

      return customClass
    }
  }
}
</script>

