<template>
  <div :class="customClass">
    <div v-if="type === 'line'" :class="[prefixClass + '__line']">
      <div :class="[prefixClass + '__back']">
        <div :class="[prefixClass + '__value', status]" 
        :style="{width: value * 100 + '%'}"></div>
      </div>
      
      <div v-if="status === ''" :class="[prefixClass + '__text']"
        style="font-size: 16px;">{{ value * 100 + '%' }}</div>
      <i v-if="status === 'error'" 
        class="cds-icon cds-icon-qingchu color-danger"></i>
      <i v-if="status === 'warning'"
        class="cds-icon cds-icon-jingshitishi" style="color: #FBBC38;"></i>
      <i v-if="status === 'success'" 
        class="cds-icon cds-icon-chenggongtishi" style="color: #43BC60;"></i>
    </div>

    <div v-if="type === 'circle'" :class="[prefixClass + '__circle']">
      <canvas :width="canvasSize" :height="canvasSize" ref="canvas"></canvas>
      <div :class="[prefixClass + '__icon', status]">
        <span v-if="status === ''" :class="[prefixClass + '__text']"
          :style="{ fontSize: size === 'large' ? '16px' : '14px'}">
          {{ value * 100 + '%' }}
        </span>
        <i v-if="status === 'error'" 
          class="cds-icon cds-icon-gantanhao color-danger"></i>
        <i v-if="status === 'warning'"
          class="cds-icon cds-icon-gantanhao"></i>
        <i v-if="status === 'success'" 
          class="cds-icon cds-icon-duihao"></i>
      </div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import drawCircle from './utils'

  const prefixClass = prefix + '-progress'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number], // 0 ~ 1
        default: 0,
      },
      type: {
        type: String,
        default: 'line', // line | circle
      },
      size: {
        type: String,
        default: 'large', // large | mini
      },
      status: {
        type: String,
        default: '',
        validator: function(value) {
          return ['', 'error', 'warning', 'success'].indexOf(value) > -1
        }
      }
    },
    data () {
      return {
        prefixClass,
        canvasSize: 80,
      }
    },
    watch: {
      'status': 'drawCircle',
      'size': 'drawCircle',
      'type': 'drawCircle',
      'value': 'drawCircle',
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'mini': 
            customClass += `${prefixClass}__mini `
            break
        }

        customClass += `${this.type} `
        return customClass
      },
    },
    created () {
      this.canvasSize = this.size === 'large' ? 80 : 48
    },
    mounted () {
      this.drawCircle()
    },
    methods: {
      drawCircle () {
        if (this.type !== 'circle') return

        let circleColor = ''

        switch (this.status) {
          case '':
            circleColor = '#31A1FF'
            break
          case 'error':
            circleColor = '#FE4040'
            break
          case 'warning':
            circleColor = '#FBBC38'
            break
          case 'success':
            circleColor = '#43BC60'
            break
        }

        const lineWidth = this.size === 'large' ? 4 : 2

        drawCircle(this.$refs.canvas, this.value * 100, circleColor, "#F5F5F5", lineWidth)
      }
    }
  }
</script>