<template>
  <div :class="customClass">
    <div v-if="$slots.header"
      :class="[prefixClass + '__header', prefixClass + '-item']">
      <slot name="header"></slot>
    </div>
    <slot></slot>
    <div v-if="$slots.footer"
      :class="[prefixClass + '__footer', prefixClass + '-item']">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-list'

  export default {
    name: prefixClass,
    props: {
      more: Boolean,
      size: {
        type: String,
        default: 'medium', // large || medium || mini
      }
    },
    data () {
      return {
        prefixClass
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break;
          case 'medium':
            customClass += `${prefixClass}__medium `
            break;
          case 'mini':
            customClass += `${prefixClass}__mini `
            break;
        }

        return customClass
      }
    },
    methods: {
      showMore () {
        this.$emit('showMore')
      }
    }
  }
</script>