<template>
  <div :class="customClass">
    <div 
    :class="`${prefixClass}__treeContainer`" 
    v-for="(item,index) in data" 
    :key="index">
      <cd-tree-item :dataList="item" :select="select" ></cd-tree-item>
    </div>
    
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  const prefixClass = prefix + '-tree'

  export default {
    name: prefixClass,
    props: {
      data: {
        type: Array,
        default: function () {
          return []
        }
      },
      defaultCheckedKey: {
        type: Array,
        default: function () {
          return []
        }
      },
      defaultExpandKey: {
        type: Array,
        default: function () {
          return []
        }
      },
      select: {
        type: Boolean,
        default: false
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        prefixClass,
        depth: 0,
        checkedList: [],
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        if (this.select) {
          customClass += `${prefixClass}__select `
        }
        
        return customClass
      }
    },
    mounted () {
      this.getLevel(this.data, 1)
      this.insertLeafNode(this.data)
  
    },
    updated () {
      this.insertLeafNode(this.data)
    },
    methods: {
      getLevel (data, level) {
        data.forEach(item => {
          item.level = level
          if (level > this.depth) {
            this.depth = level
          }
          if ('children' in item){
            if (item.children.length > 0) {
              for (let i = 0; i < item.children.length; i++) {
                let children = item.children[i]
                children.parentId = item.id
              }
              
              this.getLevel(item.children, level + 1)
            }
          }
        })
        
      },
      insertLeafNode (data) {
        data.forEach(item => {
          item.isChecked = this.defaultCheckedKey.includes(item.label)
          item.isLeaf = item.children ? false : true
          item.disabled = item.disabled ? item.disabled : false
          if ('children' in item){
            if (item.children.length > 0) {
              this.insertLeafNode(item.children)
            }
          }
        })    
      },

    }
  }
</script>