<template>
  <div :class="customClass">
    <div :class="[prefixClass + '__columns']" v-show="false">
      <slot></slot>
    </div>
    <table-header :columns="columns" />
    <table-body :columns="columns" :data="data" :calcSpan="calcSpan" />
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  import tableHeader from './table-header.vue'
  import tableBody from './table-body.js'

  const prefixClass = prefix + '-table'

  export default {
    name: prefixClass,
    components: {
      tableHeader,
      tableBody,
    },
    props: {
      data: {
        type: Array,
        default: function () {
          return []
        }
      },
      calcSpan: {
        type: Function,
        default: function () {
          return [1, 1]
        }
      }
    },
    data () {
      return {
        prefixClass,
        columns: [],
        sortable: {
          type: '',
          prop: '',
        },
        checkedRows: []
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    watch: {
      sortable: {
        deep: true,
        handler: function ({ prop, type }) {
          this.$emit('sort', { prop, type })
        }
      },
      checkedRows: {
        deep: true,
        handler: function (checkedRows) {
          this.$emit('checked', checkedRows)
        }
      }
    },
    mounted () {
      this.columns = this.$children.map(children => {
        return {
          prop: children.prop,
          label: children.label,
          width: children.calcWidth,
          renderSlot: children.renderSlot,
          isColumn: !!children.isColumn,
          sortable: children.sortable,
          type: children.type,
        }
      })
      this.columns = this.columns.filter(column => column.isColumn)
      this.columnSpan = this.calcSpan(this.columns, this.data) // 
    },
    methods: {
      toggleChecked (checked, row) {
        if (checked) {
          this.checkedRows.push(row)
        } else {
          const index = this.checkedRows.findIndex(item => item === row)

          this.checkedRows.splice(index, 1)
        }
      },
      toggleCheckAll (checked) {
        if (checked) {
          this.checkedRows = this.data
        } else {
          this.checkedRows = []
        }
      }
    }
  }
</script>