import MessageOptions from './message.vue';

var MessagePlugin = {}
let instance;
let instances = [];
let seed = 1
let zIndex = 1000

MessagePlugin.install = function (Vue, options) {
  const MessageConstructor = Vue.extend(MessageOptions);

  class Message {
    constructor (option) {
      let opt = {}
      this.id =  `message_${seed++}`

      opt = this.parseOption(option, opt)
      instance = new MessageConstructor({
        data: opt
      })
      
      instance.id = this.id
      const beforeEl = instance.$mount()
      document.body.appendChild(beforeEl.$el);
      instance.visible = true
  
      this.setOffset(option)
      this.removeInstance(instance.id)
      instances.push(instance)
    }

    parseOption (option, opt) {
      if (typeof option === 'string') {
        opt = {
          content: option,
        };
      } else {
        ['content', 'type', 'duration', 'closable'].forEach(property => {
          if (option[property]) {
            opt[property] = option[property]
          }
        })
      }

      opt.zIndex = zIndex++
      opt.onClose = () => {
        this.removeInstance(this.id)
      };

      return opt
    }

    setOffset (option) {
      let offset = option.offset || 0;
      instances.filter(item => item.position === this.position).forEach(item => {
        offset += item.$el.offsetHeight + 16;
      });

      offset += 16
      instance.offset = offset
    }

    removeInstance (id) {
      let index = -1;
      const len = instances.length;
      const instance = instances.filter((instance, i) => {
        if (instance.id === id) {
          index = i;
          return true;
        }

        return false;
      })[0];

      if (!instance) return;

      instances.splice(index, 1);
      if (len <= 1) return;

      const removedHeight = instance.$el.offsetHeight;
      for (let i = index; i < len - 1 ; i++) {
        let dom = instances[i].$el;
        dom.style['top'] =
          parseInt(dom.style['top'], 10) - removedHeight - 16 + 'px';
      }
    }
  }

  Vue.prototype.$message = (option) => {
    let message = new Message(option)
  }   

  ['success', 'info', 'warning', 'error'].forEach(type => {
    Vue.prototype.$message[type] = (option) => {
      if (typeof option === 'string') {
        option = {
          content: option
        }
      }
      
      option.type = type
      return Vue.prototype.$message(option)
    }
  })
}

export default MessagePlugin