import { prefix } from '../../utils'

const prefixClass = prefix + '-table__body'

export default {
  name: prefixClass,
  props: {
    data: Array,
    columns: Array,
    calcSpan: Function
  },
  data () {
    return {
      prefixClass,
      isShows: {},
      firstColumnIndex: 0,
    };
  },
  computed: {
    customClass () {
      let customClass = prefixClass + ' '
      
      return customClass
    }
  },
  created () {
    this.data.forEach((item, index) => {
      if (item.children) {
        item.children = item.children.map(item2 => {
          item2.isChild = true
          item2.parentIndex = index
          return item2
        })
        this.data.splice(index + 1, 0, ...item.children)
      }
    })
  },
  mounted () {
    const hasCheck = this.columns.findIndex(column => column.type === 'checkbox') > -1;

    this.firstColumnIndex = hasCheck ? 1 : 0
  },
  methods: {
    rowRender(row, $index) {
      let trClassName = ''
      let trStyle = {}

      if (row.children) {
        trClassName += `${prefix}-column__parent `
        trClassName += this.isShows[$index] ? `open ` : `close `
      } else if (row.isChild) {
        trClassName += `${prefix}-column__child `
        if (!this.isShows[row.parentIndex]) {
          trStyle.display = 'none'
        }
      }

      return (<tr class={trClassName} style={trStyle}>
        {
          this.columns.map((column, index) => {
            const [colSpan, rowSpan] = this.calcSpan(index + 1, $index + 1)
            let className = ''

            if (column.prop) {
              className += column.prop
            }
            if (column.type === 'checkbox') {
              className += ' is-checkbox'
            }

            if (!colSpan || !rowSpan) {
              return null
            }

            return (
              <td colspan={colSpan} rowspan={rowSpan} class={className}>
                { (row.children && index === this.firstColumnIndex) 
                  && 
                  <i class="cds-icon cds-icon-top" onClick={() => this.toggleChildren($index)}></i> 
                }
                {
                  column.renderSlot.call(
                    this._renderProxy,
                    this.$createElement,
                    { column, row, $index },
                  )
                }
              </td>
            );
          })
        }
      </tr>);
    },
    toggleChildren ($index) {
      this.$set(this.isShows, $index, !this.isShows[$index])
    }
  },
  render (h) {
    const data = this.data
    const columns = this.columns
    const style = {
      width: '100%'
    }

    return (
      <div class={this.customClass}>
        <table style={style} cellpadding="0" cellspacing="0">
          <colgroup>
            {
              columns.map(column => <col name={column.prop} width={column.width} />)
            }
          </colgroup>
          <tbody>
            {
              data.reduce((arr, row, index) => {
                return arr.concat(this.rowRender(row, arr.length));
              }, [])
            }
          </tbody>
        </table>
      </div>
    )
  }
}