<template>
  <div :class="customClass">
    <div :class="[prefixClass + '__header']" v-show="!hiddenHeader">
      <div :class="[prefixClass + '__title']">{{ title }}</div>
      <div :class="[prefixClass + '__action']">
        <slot name="action"></slot>
      </div>
    </div>
    <div :class="[prefixClass + '__body']">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-card'

  export default {
    name: prefixClass,
    props: {
      title: {
        type: String,
        default: ''
      },
      'hidden-header': {
        type: Boolean,
        default: false,
      }
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      },
    },
    methods: {
    }
  }
</script>