<template>
  <div :class="customClass" ref="datePicker">
    <input type="text" 
      :value="innerValue" 
      :disabled="disabled" 
      @focus="showDatePanel"
      readonly 
    />
    <i class="cds-icon cds-icon-riqi"></i>
    <div :class="prefixClass + '__panel'" v-show="isShowDatePanel">
      <div :class="prefixClass + '__current'">
        <i class="cds-icon cds-icon-xiangshang prev-year pull-left"
          @click="changeYear(currentYear - 1)"></i>
        <i v-show="currentView === 1" 
          class="cds-icon cds-icon-left prev-month pull-left"
          @click="changeMonth(currentMonth - 1)"></i>
        <span @click="changeCurrentView(currentView + 1)" style="cursor: pointer;">
          <span>{{ currentYear }}</span> 
          <span v-show="currentView === 1">{{ currentMonth + 1 }}</span>
        </span>
        <i class="cds-icon cds-icon-xiangshang next-year pull-right"
          @click="changeYear(currentYear + 1)"></i>
        <i v-show="currentView === 1" 
          class="cds-icon cds-icon-left next-month pull-right"
          @click="changeMonth(currentMonth + 1)"></i>
      </div>
      <!--  -->
      <cd-day v-model="currentDay" 
        v-show="currentView === 1"
        @change="isShowDatePanel = false"
        :minDate="minDate"
        :maxDate="maxDate"
        :year="currentYear"
        :month="currentMonth" 
      />
      <cd-month v-show="currentView === 2" 
        v-model="currentMonth"
        @change="changeCurrentView(1)"
      />
      <cd-year v-show="currentView === 3" 
        v-model="currentYear"
        @change="changeCurrentView(2)"
      />
      <div :class="prefixClass + '__datetime'" v-if="type === 'datetime' && startView === 1">
        <cd-time v-model="time"></cd-time>
      </div>
    </div>
  </div>
</template>

<script>
  import dayjs from 'dayjs'

  import { prefix } from '../../utils'
  import CdDay from './day.vue'
  import CdMonth from './month.vue'
  import CdYear from './year.vue'
  import CdTime from '../components/time.vue'

  const prefixClass = prefix + '-date-picker'

  export default {
    name: prefixClass,
    components: {
      CdDay,
      CdMonth,
      CdYear,
      CdTime,
    },
    props: {
      value: {
        type: String,
        required: true,
      },
      type: {
        type: String,
        default: 'date'  // date || datetime
      },
      size: {
        type: String,
        default: 'medium'
      },
      disabled: {
        type: Boolean,
        default: false
      },
      startView: {
        type: Number, // 3-year || 2-month || 1-day
        default: 1,
        validator: function(value) {
          return value <= 3
        }
      },
      endView: {
        type: Number, // 3-year || 2-month || 1-day
        default: 3,
        validator: function(value) {
          return value >= 1
        }
      },
      minDate: {
        type: [String, Object],
        validator: function(value) {
          if (value === undefined) return true

          if (!(value instanceof dayjs)) {
            return dayjs(value).isValid()
          }

          return true
        }
      },
      maxDate: {
        type: [String, Object],
        validator: function(value) {
          if (value === undefined) return true

          if (!(value instanceof dayjs)) {
            return dayjs(value).isValid()
          }

          return true
        }
      },
      placeholder: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        prefixClass,
        innerValue: '',
        currentView: this.startView,
        currentYear: '',
        currentMonth: '',
        currentDay: {},
        isShowDatePanel: false,
        time: '',
      }
    },
    watch: {
      currentDay: {
        deep: true,
        handler: function() {
          const { $y, $M, $D } = this.currentDay
          const month = $M < 9 ? `0${$M + 1}` : ($M + 1)
          const day = $D < 10 ? `0${$D}` : $D
          const diff = this.endView - this.startView
          let date = ''

          switch (diff) {
            case 0:
              date = this.endView === 3 ? `${$y}`
                : this.endView === 2 ? `${month}`
                : `${day}`
              break
            case 1:
              date = this.endView === 3 ? `${$y}-${month}` : `${month}-${day}`
              break
            case 2:
              date = `${$y}-${month}-${day}`
              break
          }

          if (this.startView === 1 && this.type === 'datetime') {
            date += ` ${this.time}`
          }

          this.innerValue = date
          this.$emit('input', this.innerValue)
        }
      },
      currentMonth(month) {
        this.currentDay = this.currentDay.month(month)
      },
      currentYear(year) {
        this.currentDay = this.currentDay.year(year)
      },
      time(time) {
        const [hour, minute, second] = time.split(':')

        this.currentDay = this.currentDay.hour(hour).minute(minute).second(second)
      }
    },   
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break
          case 'mini':
            customClass += `${prefixClass}__mini `
            break
        }

        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        if (this.type === 'datetime') {
          customClass += `is-datetime `
        }

        return customClass
      }
    },
    created() {
      if (this.startView > this.endView) {
        throw new Error('startView can not be greater than endView')
      }

      this.initData()
      window.addEventListener('click', this.hideDatePanel.bind(this))
    },
    beforeDestroy() {
      window.removeEventListener('click', this.hideDatePanel.bind(this))
    },
    methods: {
      initData() {
        const time = dayjs(this.value)

        this.innerValue = this.value
        this.currentDay = time.isValid() ? time : dayjs()
        this.currentYear = this.currentDay.$y
        this.currentMonth = this.currentDay.$M

        if (this.type === 'datetime' && this.startView === 1) {
          const { $H, $m, $s } = this.currentDay
          this.time = `${$H}:${$m}:${$s}`
        }
      },
      changeYear(year) {
        if (this.currentView === 3) {
          const calcYear = year > this.currentYear ? this.currentYear + 12 : this.currentYear - 12;
          this.currentYear = Math.max(1970, calcYear)
        } else {
          this.currentYear = Math.max(1970, year)
        }
      },
      changeMonth(month) {
        if (month < 0) {
          this.currentYear--
          this.currentMonth = 12 + month
        } else if (month > 11) {
          this.currentYear++
          this.currentMonth = month - 12
        } else {
          this.currentMonth = month
        }
      },
      changeCurrentView(view) {
        if (view >= this.startView && view <= this.endView) {
          this.currentView = view
        }
      },
      hideDatePanel(event) {
        if (this.validateChild(event.target)) return

        this.isShowDatePanel = false
      },
      showDatePanel(event) {
        this.isShowDatePanel = true
      },
      validateChild(dom) {
        const parentNode = dom.parentNode

        if (!parentNode) {
          return false
        }

        if (parentNode === this.$refs.datePicker) {
          return true
        } else {
          return this.validateChild(parentNode)
        }
      }
    }
  }
</script>