<template>
  <div :class="customClass">
    <slot></slot>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-list-item'

  export default {
    name: prefixClass,
    props: {
      size: String,
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break;
          case 'medium':
            customClass += `${prefixClass}__medium `
            break;
          case 'mini':
            customClass += `${prefixClass}__mini `
            break;
        }
        
        return customClass
      }
    },
    methods: {
    }
  }
</script>