<template>
  <div :class="customClass" 
    :style="{'padding-left': $parent.mode === 'inline' ? '0' : $parent.labelWidth}">
    <label 
      :class="prefixClass + '__label'"
      :style="labelStyle">
      {{ label }}
    </label>
    <div :class="prefixClass + '__body'">
      <slot />
      <div :class="prefixClass + '__error'" v-show="message">{{message}}</div>
    </div>
  </div>
</template>

<script>
  import { prefix, isEmptyArray } from '../../utils'

  import validator from 'validator'

  const prefixClass = prefix + '-form-item'

  const funcName = {
    required: 'isEmpty',
    date: 'isDate',
    boolean: 'isBoolean',
    email: 'isEmail',
    telephone: 'isMobilePhone',
    number: 'isNumeric',
    url: 'isURL',
  }

  export default {
    name: prefixClass,
    props: {
      label: {
        type: String,
        required: true,
      },
      prop: {
        type: String,
      },
      rules: {
        type: Array,
        default: function() {
          return []
        }
      }
    },
    data () {
      return {
        prefixClass,
        message: '',
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.message) {
          customClass += 'is-error'
        }
        
        return customClass
      },
      currentRules() {
        let rules = this.rules.slice(0)
        const parent_rules = this.$parent.rules[this.prop]

        if (parent_rules) {
          rules = rules.concat(parent_rules)
        }

        return rules || []
      }
    },
    created() {
      const labelWidth = this.$parent.labelWidth
      if (this.$parent.mode === 'inline') {
        this.labelStyle = {
          width: labelWidth,
        }
      } else {
        this.labelStyle = { 
          width: labelWidth,
          position: 'absolute',
          top: 0,
          left: 0,
          display: 'block',
        }
      }
    },
    mounted() {
      this.validate()
    },
    methods: {
      validate(immediate) {
        const children = this.$children[0]

        this.currentRules.forEach(rule => {
          if (!rule.trigger || (Array.isArray(rule.trigger) && !rule.trigger.length)) return

          children.$on(rule.trigger, this.handleValidate.bind(this, rule))

          if (immediate) {
            this.handleValidate(rule, children.value)
          }
        })

        return this.message
      },
      handleValidate(rule, value) {
        if (rule.validator) {
          return this.customValidator(rule.validator)
        }

        if (rule.required && this.checkEmpty(value, rule)) {
          this.message = rule.message || `${this.label}`
          return
        }

        if (rule.type && !this.checkType(value, rule.type)) {
          this.message = rule.message
          return
        }

        this.message = ''
      },
      customValidator(validator) {
        return rule.validator(rule, value, (error) => {
          if (typeof error == "object") {
            message = error.message
          } else {
            message = error
          }
        })
      },
      checkEmpty(value, rule) {
        if (Array.isArray(value)) {
          if (value.length === 0) {
            return true
          }

          return value.some(item => validator.isEmpty(item.toString()))
        }

        if (typeof value === 'string' && validator.isEmpty(value)) {
          return true
        }

        return false
      },
      checkType(value, type) {
        const api = funcName[type]
        if (typeof validator[api] !== 'function') return

        if (type === 'telephone') {
          return validator[api](value, 'zh-CN')
        }

        return validator[api](value)
      }
    }
  }
</script>