<template>
  <div>
    <slot v-bind:row="row"></slot>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import CdCheckbox from '../checkbox'

  const prefixClass = prefix + '-table-column'

  export default {
    name: prefixClass,
    components: {
      CdCheckbox
    },
    props: {
      prop: {
        type: String,
      },
      label: {
        type: String,
      },
      width: {
        type: [String, Number],
      },
      sortable: Boolean,
      type: String,
    },
    data () {
      return {
        prefixClass,
        row: {},
        calcWidth: this.width || 180,
        currentIndex: 0,
        renderSlot: null,
        isColumn: true,
        $table: {}
      };
    },
    created () {
      this.$table = this.getParentTableVnode()
    },
    mounted () {
      const { data, $children } = this.$parent

      this.currentIndex = $children.findIndex(children => children === this)
      this.row = data[this.currentIndex]
      this.renderSlot = (h, data) => {
        let html = null;

        if (this.$scopedSlots.default) {
          html = this.$scopedSlots.default(data);
        } else {
          html = data.row[data.column.prop];
        }

        return (
          <div class="td__inner">
            { this.type === 'checkbox' ?
              <cds-checkbox checked={this.isChecked(data.row)} 
                onChange={checked => this.$table.toggleChecked(checked, data.row)}>
              </cds-checkbox>
              : html
            }
          </div>
        );
      };
      
      const lastIndex = $children.findIndex(children => {
        return !children.prop && children.type !== 'checkbox'
      }) - 1

      if (this.currentIndex === lastIndex) {
        this.calcWidth = 0
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
      getParentTableVnode () {
        let $parent = this.$parent
        let index = 0

        while (index++ < 100) {
          if (!$parent) {
            return {}
          } else if ($parent.$options.name === `${prefix}-table`) {
            return $parent
          } else {
            $parent = $parent.$parent
          }
        }
      },
      isChecked (row) {
        return this.$table.checkedRows.findIndex(item => item === row) > -1
      },
    }
  }
</script>