<template>
  <div :class="customClass">
    <div v-for="item in [1,2,3,4,5]" :key="item"
      :class="[prefixClass + '__item']"
      @click="choose(item)">
      <i :class="['cds-icon cds-icon-xingxing', 
        prefixClass + '__icon', 
        {'active': item <= value}]"
        :style="{ color: item <= value ? color: '', }">
        <i v-if="(item - value > 0) && (item - value < 1)"
          :class="['cds-icon cds-icon-xingxing active', prefixClass + '__inner']"
          :style="{ width: (value + 1 - item) * 100 + '%', color, }"></i>
      </i>
    </div>
    <div v-if="showScore" :class="[prefixClass + '__score']">
      {{ innerValue }}
    </div>
  </div>
</template>

<script>
/*
 * 
*/
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-rate'

  export default {
    name: prefixClass,
    props: {
      value: [String, Number],
      'show-score': Boolean,
      disabled: Boolean,
      color: {
        type: String,
        default: '#fbbc38',
      },
      unit: {
        type: String,
        default: ''
      },
      readonly: Boolean,
    },
    data () {
      return {
        prefixClass,
        innerValue: ''
      }
    },
    watch: {
      value: {
        immediate: true,
        handler: function (value) {
          this.innerValue = value + this.unit
        }
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
      isFloat (number) {
        if (Object.prototype.toString.call(number) !== '[object Number]') return false

        return number.toString().indexOf('.') > -1
      },
      choose (value) {
        if (this.readonly) return

        this.$emit('change', value)
        this.$emit('input', value)
      }
    }
  }
</script>