<template>
  <div :class="customClass" @click="handleChoose" :checked = checked>
    <div :class="boxClass">
      <div v-if="indeterminate" :class="[`${prefixClass}__jianhao`]" @click="handleChoose"></div>
      <i v-if="active" :class="[`${prefix}-icon `, `${prefix}-icon-duihao `]" ></i>
    </div>
    <div :class="prefixClass + '__label'">
      <slot />
      <template v-if="!$slots.default">{{ label }}</template>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-checkbox'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number, Boolean],
      },
      label: {
        type: [String, Number],
      },
      checked: {
        type: Boolean,
        default: false
      },
      disabled: {
        type: Boolean,
        default: false
      },
      indeterminate: {
        type: Boolean,
        default: false
      },
      onCheckAll: {
        type: Function,
        default: null
      }
    },
    data () {
      return {
        prefix,
        prefixClass,
        active: this.checked
      }
    },
    watch: {
      checked (value) {
        this.active = value
      }
    },
    computed: {
      isDisabled() {
        return this.$parent.disabled || this.disabled
      },
      customClass () {
        let customClass = prefixClass + ' '

        if (this.isDisabled) {
          customClass += prefixClass + '__disabled '
        }

        if (this.indeterminate) {
          customClass += prefixClass + '__indeterminate '
        }
        
        return customClass
      },
      boxClass() {
        let boxClass = prefixClass + '__box '

        if (this.active) {
          boxClass += 'active '
        }

        return boxClass
      },
      isGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.name !== `${prefix}-checkbox-group`) {
            parent = parent.$parent;
          }
          else {
            return true;
          }
        }
        return false;
      },
      groupParent () {
        let parent = this.$parent;

        while (parent && parent.$options.name !== `${prefix}-checkbox-group` ) {
          parent = parent.$parent;
        }
        
        return parent;
      }
    },
    mounted() {
      this.$on('handleChoose', this.handleChoose)
    },
    watch: {
      checked () {
        this.active = this.checked
      }
    },
    methods: {
      handleChoose() {
        this.active = !this.active;
        
        this.$emit('input', this.value)
        this.$emit('change', this.active)

        if (this.isGroup) {
          const parentValue = this.groupParent.value.slice()
          
          if (!this.active) {
            const index = parentValue.indexOf(this.value)
            
            if (index > -1) {
              parentValue.splice(index, 1)
            }
          } else {
            parentValue.push(this.value)
          }
          
          this.groupParent.$emit('input', parentValue)
          this.groupParent.$emit('change', parentValue)
        }

      }
    }
  }
</script>