<template>
  <div :class="customClass">
    <upload-list
        v-if="listType === 'picture-card'"
        :list-type="listType"
        :files="uploadFiles"
        :on-remove="handleRemove"
        :on-reupload="handleReUpload"
    />
    <upload-container
        v-show="listType === 'file' || !limit || uploadFiles.length < limit"
        ref="upload-container"
        :drag="drag"
        :action="action"
        :list-type="listType"
        :on-start="handleStart"
        :on-progress="handleProgress"
        :on-success="handleSuccess"
        :on-error="handleError"
        :on-remove="handleRemove"
    >
      <upload-list
          v-if="listType === 'file'"
          :list-type="listType"
          :files="uploadFiles"
          :on-remove="handleRemove"
          :on-reupload="handleReUpload"
      />
      <slot v-else></slot>
    </upload-container>
    <upload-list
        v-if="['picture-card', 'file'].indexOf(listType) === -1"
        :list-type="listType"
        :files="uploadFiles"
        :on-remove="handleRemove"
        :on-reupload="handleReUpload"
    />
    <slot name="tip"/>
  </div>
</template>

<script>
import {prefix} from '../../utils'
import UploadContainer from './upload-container.vue'
import UploadList from './upload-list.vue'

const prefixClass = prefix + '-upload'

function noop() {
}

export default {
  name: prefixClass,
  components: {
    UploadContainer,
    UploadList,
  },
  props: {
    action: {
      type: String,
      required: true,
    },
    drag: Boolean,
    listType: {
      type: String,
      default: 'text'
    },
    limit: Number
  },
  data() {
    return {
      prefixClass,
      uploadFiles: [],
      tempIndex: 1,
    };
  },
  computed: {
    customClass() {
      let customClass = prefixClass + ' '

      return customClass
    }
  },
  methods: {
    handleStart(rawFile) {
      rawFile.uid = Date.now() + this.tempIndex++
      let file = {
        status: 'ready',
        name: rawFile.name,
        size: rawFile.size,
        percentage: 0,
        uid: rawFile.uid,
        raw: rawFile
      }

      if (this.listType === 'picture-card') {
        try {
          file.url = URL.createObjectURL(rawFile)
        } catch (e) {
          console.error(e)
        }
      }

      this.uploadFiles.push(file)
    },
    handleProgress(e, rawFile) {
      const file = this.getFile(rawFile)
      file.status = 'uploading';
      file.percentage = e.percent || 0;
    },
    getFile(rawFile) {
      let fileList = this.uploadFiles
      return fileList.find(item => item.uid === rawFile.uid)
    },
    handleSuccess(res, rawFile) {
      const file = this.getFile(rawFile);

      if (file) {
        file.status = 'success';
        file.response = res;
      }
    },
    handleError(err, rawFile) {
      const file = this.getFile(rawFile);
      const fileList = this.uploadFiles;

      file.status = 'fail';
    },
    handleRemove(file, raw) {
      if (raw) {
        file = this.getFile(raw);
      }
      let doRemove = () => {
        // this.abort(file);
        let fileList = this.uploadFiles;
        fileList.splice(fileList.indexOf(file), 1);
      };

      doRemove();
    },
    handleReUpload(file, raw) {
      if (raw) {
        file = this.getFile(raw);
      }

      this.$refs['upload-container'].post(file.raw)
    }

    // 'on-exceed': this.onExceed,
    // 'on-preview': this.onPreview,
  }
}
</script>