<template>
  <ul :class="customClass">
    <li v-for="(menu, index) in menus"
      :class="[{ open: openMenu[index] }]" 
      :key="menu.title" 
      @click="toggle(menu, index)">
      <div :class="[prefixClass + '__text']">{{ menu.title }}</div>
      <i class="cds-icon cds-icon-top"></i>
      <div :class="[prefixClass + '-sub']"
        :style="{
          height: openMenu[index] ? heights[`sub${index}`] : 0,
          padding: !openMenu[index] && 0,
        }">
        <ul v-if="menu.children && menu.children.length"
          :ref="'sub' + index" >
          <li v-for="menu2 in menu.children" 
            :key="menu2.title"
            :class="[{active: currentMenu === menu2}]"
            @click="(event) => menuPath(menu2, event)">{{menu2.title}}</li>
        </ul>
      </div>
    </li>
  </ul>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-navmenu'
  
  export default {
    name: prefixClass,
    props: {
      menus: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data () {
      return {
        prefixClass,
        openMenu: {},
        heights: {},
        subs: {},
        currentMenu: null
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    watch: {
      'menus.length': function () {
        this.$nextTick(function() {
          this.getSubs()
        })
      }
    },
    mounted () {
      this.getSubs()
      this.calcHieght()
      this.getCurrentMenu()
    },
    methods: {
      toggle(menu, index) {
        if (menu.children) {
          this.$set(this.openMenu, index, !this.openMenu[index])
          this.calcSingleHieght(`sub${index}`)
          return
        }

        this.menuPath(menu)
      },
      menuPath (menu, event) {
        if (event) {
          event.stopPropagation()
        }
        
        if (menu.link) {
          window.location.href = menu.link
        } else if (menu.to) {
          this.$router && this.$router.push(menu.to)
        }

        this.currentMenu = menu
      },
      getCurrentMenu () {
        const currentPath = this.$route.path

        for (let i = 0; i < this.menus.length; i++) {
          const menu = this.menus[i]

          if (menu.to === currentPath) {
            this.currentMenu = menu
            break
          }

          if (menu.children) {
            const index = menu.children.findIndex(menu2 => {
              return menu2.to === currentPath
            })

            if (index > -1) {
              this.currentMenu = menu.children[index]
              break
            }
          }
        }
      },
      getSubs () {
        const subs = {}
        for (const key in this.$refs) {
          if (key.startsWith('sub')) {
            subs[key] = this.$refs[key]
          }
        }
        this.subs = subs
      },
      calcSingleHieght (key) {
        try {
          let height = window.getComputedStyle(this.subs[key][0]).height

          height = parseFloat(height) + 'px'
          this.$set(this.heights, key, height)
        } catch (e) {
          console.log('calcSingleHieght: ', e)
        }
      },
      calcHieght () {
        for(const key in this.subs) {
          this.calcSingleHieght(key)
        }
      }
    }
  }
</script>