<template>
  <div :class="customClass">
    <ul :class="prefixClass + '__weekday'">
      <li v-for="item in weekday">{{ item }}</li>
    </ul>
    <ul :class="prefixClass + '__day'">
      <li v-for="item in days"
        @click="chooseCurrentDay(item)"
        :class="{
          'disabled': (minDate && item < dayjs_minDate) || (maxDate && item > dayjs_maxDate),
          'active': (item === value || (item.$y === value.$y) && (item.$M === value.$M) && (item.$D === value.$D)),
        }"
        :key="item.$y + '-' + item.$M + '-' + item.$D">
        <div 
          :class="{
            'is-current': (item === today || (item.$y === today.$y) && (item.$M === today.$M) && (item.$D === today.$D)),
            'is-not': item.$M !== month,
          }"
        >{{ item.$D | day }}</div>
        <div :class="[prefixClass + '__todolist']">
          
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import dayjs from 'dayjs'

  const prefixClass = prefix + '-calender-day'

  export default {
    name: prefixClass,
    props: {
      todoList: Array,
      value: {
        type: Object,
        default: function() {
          return {}
        }
      },
      year: {
        type: [String, Number],
        default: function() {
          return dayjs().$y
        }
      },
      month: {
        type: [String, Number],
        default: function() {
          return dayjs().$M
        }
      },
      minDate: {
        type: [String, Object],
      },
      maxDate: {
        type: [String, Object],
      }
    },
    data () {
      return {
        prefixClass,
        today: dayjs(),
        weekday: ['', '', '', '', '', '', ''],
        days: [],
        dayjs_minDate: {},
        dayjs_maxDate: {},
      }
    },
    watch: {
      minDate: {
        deep: true,
        handler: 'initMinDate',
      },
      maxDate: {
        deep: true,
        handler: 'initMaxDate',
      },
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    watch: {
      year: 'handleGetDays',
      month: 'handleGetDays',
    },
    created() {
      this.handleGetDays()
      this.initMinDate()
      this.initMaxDate()
    },
    methods: {
      handleGetDays() {
        this.getDays(this.year, this.month)
      },
      getDays(year, month) {
        const days = []
        const currentDay = dayjs(`${year}-${month + 1}-1`)
        const currentMonthFirstDay = currentDay.startOf('month')
        const currentMonthLastDay = currentDay.endOf('month')

        for (let i = currentMonthFirstDay.$W; i > 0; i--) {
          days.push(currentMonthFirstDay.subtract(i, 'day'))
        }

        for (let i = 0; i < currentMonthLastDay.$D; i++) {
          days.push(currentMonthFirstDay.add(i, 'day'))
        }

        for (let i = 1; i <= 6 - currentMonthLastDay.$W; i++) {
          days.push(currentMonthLastDay.add(i, 'day'))
        }

        this.days = days
      },
      chooseCurrentDay(value) {
        if (this.isDiabled(value)) return

        if (value.$M !== this.month) return

        this.$emit('input', value)
        this.$emit('change', value)
      },
      initMinDate() {
        if (this.minDate === undefined) return

        if (this.minDate instanceof dayjs) {
          this.dayjs_minDate = this.minDate
        } else {
          this.dayjs_minDate = dayjs(this.minDate)
        }
      },
      initMaxDate() {
        if (this.maxDate === undefined) return

        if (this.maxDate instanceof dayjs) {
          this.dayjs_maxDate = this.maxDate
        } else {
          this.dayjs_maxDate = dayjs(this.maxDate)
        }
      },
      isDiabled(value) {
        return  (this.minDate && value < this.dayjs_minDate) || (this.maxDate && value > this.dayjs_maxDate)
      }
    },
    filters: {
      day(value) {
        return value < 10 ? `0${value}` : value
      }
    }
  }
</script>