<template>
  <div :class="customClass">
    <div :class="wrapperClass"  @click.stop="changeFocus">
      <div :class="[`${prefix}-select__wrapper-container `]">
        <div :class="[`${prefix}-select__tags `]" v-if="multiple">
          <cd-tag v-for="(item,index) in list" :key="index" closable :size="size" @close="handleClose(item)">
            {{item}}
          </cd-tag>
        </div>   
        <div :class="[`${prefix}-select-input `]" v-if="isInputShow">
          <input type="text" :class="[`${prefix}-select__inner `]" v-model="innerValue" :placeholder="this.placeholder" >
        </div>
      </div>
      <span :class="[`${prefix}-select__wrapper-icon `]">
        <i :class="[`${prefix}-icon `,  `${prefix}-icon-top `]"></i>
      </span>
    </div>

    <ul :class="[`${prefix}-select__option-list `]" v-show="isFocus" >
        <slot></slot> 
    </ul>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  const prefixClass = prefix + '-select'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String,Number,Boolean,Array],
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      size: {
        type: String,
				default: 'medium'
      },
      type: {
        type: String,
				default: 'default'
      },
      multiple: {
        type: [Boolean, String],
        default: false
      },
    },
    data () {
      return {
        prefix,
        prefixClass,
        isFocus: false,
        label: '',
        innerValue: this.label,
        readonly: true,
        list:[],
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if(this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break;
          case 'medium':
            customClass += `${prefixClass}__medium `
            break;
          case 'mini':
            customClass += `${prefixClass}__mini `
            break;
        }

        if(this.type === 'search') {
          this.readonly = false
          customClass += prefixClass + '__search '
        }				

        return customClass
      },
      wrapperClass () {
        let wrapperClass = prefixClass + '__wrapper '

        if(this.isFocus) {
          wrapperClass += prefixClass + '__wrapper__focus '
        }

        return wrapperClass
      },
      isInputShow () {
        if(this.multiple && this.type !== 'search') {
          if (this.value.length === 0) {
            return true
          }
          return false

        }
        return true
      },
      placeholder () {
        if(this.type === 'search') {
          return ''
        }
        return ''
      }
    },
    watch: {
      value () {
        this.innerValue = this.label       
      },
      label () {
        this.innerValue = this.label 
      },
    },
    created () {
      if (this.multiple && !Array.isArray(this.value)) {
        this.$emit('input', []);
      }
      if (!this.multiple && Array.isArray(this.value)) {
        this.$emit('input', '');
      }
    },
    mounted () {
      const that = this
      document.addEventListener('click', function() {
        that.isFocus = false
      })
    },
    methods: {
      changeFocus () {
        this.isFocus = !this.isFocus   
      },
      setLabel (value) {
        this.label = value
      },
      handleClose (tag) {
        const index = this.list.indexOf(tag)
        this.list.splice(index, 1);
        this.value.splice(index, 1);
      }
    },
    
  }
</script>