<template>
  <div :class="customClass">
    <slot></slot>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import Sortablejs from 'sortablejs'

  const prefixClass = prefix + '-sortable'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: Array,
        required: true,
        default: function () {
          return []
        }
      },
      options: Object
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    mounted () {
      const options = Object.assign({
        animation: 150,
        ghostClass: 'is-chosen',
        onEnd: (event) => {
          const { oldIndex, newIndex } = event

          const data = this.value.slice()
          const [sortableItem] = data.splice(oldIndex, 1)
          
          data.splice(newIndex, 0, sortableItem)
          this.$emit('input', data)
        }
      }, this.options)

      new Sortablejs(this.$el, options)
    },
    methods: {
    }
  }
</script>