<template>
    <span :class="customClass" :closable="closable" :color="color" v-if="isClosed">
      <span :class="[`${prefix}-tag-font`]"><slot></slot></span>
      <i :class="[`${prefix}-icon `, `${prefix}-icon-guanbi `]" v-if="closable" @click="handleClose"></i>
    </span>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-tag'

  export default {
    name: prefixClass,
    props: {
      closable: {
        type: Boolean,
        default: false
      },
      color: {
        type: String,
        default: 'default'   // default|blue|green|orange|red
      },
      size: {
        type: String,
        default: 'small'    // large|medium|small|mini
      },
      theme: {
        type: String,
        default: 'light'     // dark|plain|light
      }
    },
    data () {
      return {
        prefix,
        prefixClass,
        isClosed: true,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        switch(this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break
          case 'small':
            customClass += `${prefixClass}__small `
            break
          case 'mini':
            customClass += `${prefixClass}__mini `
            break
        }

        switch(this.color) {
          case 'default':
            customClass += `${prefixClass}__defalut `
            break
          case 'blue':
            customClass += `${prefixClass}__blue `
            break
          case 'green':
            customClass += `${prefixClass}__green `
            break
          case 'orange':
            customClass += `${prefixClass}__orange `
            break
          case 'red':
            customClass += `${prefixClass}__red `
            break
        }

        switch(this.theme) {
          case 'dark':
            customClass += `${prefixClass}__dark `
            break
          case 'light':
            customClass += `${prefixClass}__light `
            break
          case 'plain':
            customClass += `${prefixClass}__plain `
            break
        }

        if(this.closable) {
          customClass += `${prefixClass}__closable `
        }

        return customClass
      }
    },
    methods: {
      handleClose(event) {
        event.stopPropagation();
        this.$emit('close', event);
      },
      handleClick(event) {
        this.$emit('click', event);
      }
    }
  }
</script>