<template>
  <ul :class="customClass">
    <li v-for="(item, index) in years"
      :class="{ 'active': item == value }" 
      @click="chooseYear(item)"
      :key="index"
    >{{ item }}</li>
  </ul>
</template>

<script>
  import dayjs from 'dayjs'

  import { prefix } from '../../utils'

  const prefixClass = prefix + '-year'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number],
        default: dayjs().$y
      }
    },
    data () {
      return {
        prefixClass,
        years: [],
      }
    },
    created() {
      this.getYears()
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    watch: {
      value: 'getYears'
    },
    methods: {
      chooseYear(year) {
        this.$emit('input', year)
        this.$emit('change', year)
      },
      getYears() {
        const years = []
        for (let i = 0; i < 12; i++) {
          years.push(this.value + i)
        }

        this.years = years
      }
    }
  }
</script>