<template>
  <div :class="customClass" ref="slider" @mousemove="mousemove"
          @mouseup="mouseup">
    <div :class="[prefixClass + '__block']">
      <div :class="[prefixClass + '__line']" ref="line" @click="choose">
        <div :class="[prefixClass + '__choosen']"
          :style="{width: Math.abs(left_2 - left_1) + '%', left: ((left_1 < left_2) ? left_1 : left_2) + '%'}"
          v-if="range"></div>
        <div :class="[prefixClass + '__choosen']"
        :style="{width: left + '%'}"
        v-else></div>

        <cd-tooltip :content="String(numRange[0]) + tag" 
        v-if="range"
        :manual="true"
        :style="{left: left_1 + '%'}">
          <div :class="[prefixClass + '__point']" 
          @mousedown="mousedown"
          :id="[prefixClass + '__pointLeft']"></div>
        </cd-tooltip>
        
        <cd-tooltip :content="String(numRange[1]) + tag" 
        v-if="range"
        :manual="true"
        :style="{left: left_2 + '%'}"
        >
          <div :class="[prefixClass + '__point']" 
          @mousedown="mousedown"
          :id="[prefixClass + '__pointRight']"
          ></div>
        </cd-tooltip>  

        <template v-else-if="showPoint">
          <cd-tooltip :content="String(item) + tag"
          v-for="(item,index) in this.tags"
          :key="index"
          :manual="true"
          :style="{left: (item * (100 / max)) + '%'}">
            <div :class="pointClass(item)" 
            @mousedown="mousedown">
            </div>
          </cd-tooltip>
        </template>
        
        <cd-tooltip :content="String(num) + tag"
        :style="{left: left + '%'}"
        :manual="true"
        v-else>
          <div :class="[prefixClass + '__point']" 
          @mousedown="mousedown">
          </div>
        </cd-tooltip>
        
        
      </div>

      <div :class="[prefixClass + '__interval']"  onselectstart="return false;" >
        <div v-for="(item,index) in this.tags" :key="index" :style="{left: (item * (100 / max)) + '%'}" @click="handleClick(item)">
          {{item + tag}}
        </div>
      </div>
    </div>

    <div :class="[prefixClass + '__input']" v-if="!showPoint" :disabled="disabled">
      <template v-if="range">
        <input type="text" v-model="numRange[0]" @blur="handelBlur" @keydown.enter="handelBlur">
        <div class="rangeSymbol" >-</div>
        <input type="text" v-model="numRange[1]" v-if="range" @blur="handelBlur" @keydown.enter="handelBlur">
      </template>
      <input type="text" v-model="num" v-else @blur="handelBlur" @keydown.enter="handelBlur">
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-slider'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number, Array],
        default: 0    
      },
      type: {
        type: String, // '' | range
        default: '',
      },
      min: {
        type: [String, Number],
        default: 0
      },
      max: {
        type: [String, Number],
        default: 0
      },
      interval: { // minmax
        type: [String, Number],
        default: 1,
      },
      tag: { // 
        type: String,
        default: ''
      },
      unit: { // 
        type: [String, Number],
        default: 1
      },
      disabled: Boolean,
      showInput : {
        type: Boolean,
        default: false
      },
      showPoint : {
        type: Boolean,
        default: false
      },
      range: {
        type: Boolean,
        default: false
      },
      

    },
    data () {
      return {
        prefixClass,
        mouseStatus: '', // down | moving | up
        mouseDir:'',     // left | right
        offsetLeft: 0,
        positionLeft: 0,
        clientWidth: 0,
        left: this.value * (100 / Number(this.max)),
        left_1: 0,
        left_2 : 0,
        num: 0,
        numRange: [0,0],
        choosen: false,
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if(this.showPoint) {
          customClass += `${prefixClass}__showPoint ` 
        }
        else {
          customClass += `${prefixClass}__notShowPoint `
        }

        if(this.disabled) {
          customClass += `${prefixClass}__disabled `
        }
        return customClass
      },
      pointClass () {
        return function(item) {
          let pointClass = prefixClass + '__point '
            if(this.left < (item * (100 / this.max))) {
              pointClass += `${prefixClass}__point-lt ` 
            }
            if(this.left == (item * (100 / this.max))) {
              pointClass += `${prefixClass}__point-eq ` 
              if(this.mouseStatus === 'down') {
                pointClass += `${prefixClass}__point-active ` 
              }
            }
            if(this.left > (item * (100 / this.max))) {
              pointClass += `${prefixClass}__point-gt ` 
            }

            return pointClass
        }
      },
      tags () {
        let tags= []
        let num = this.min
        const space = (Number(this.max) - Number(this.min)) / Number(this.interval)
        for(let i = 0; i <= Number(this.interval); i++) {
          tags[i] = Math.ceil(num)
          num += space
        }

        return tags
      },
      decimals () {
        const x = String(this.unit).indexOf('.') + 1;   //
        if (x) {
          return String(this.unit).length - x;    //
        }
        return 0;
      }
    },
    mounted () {
      this.clientWidth = this.$refs.line ? this.$refs.line.clientWidth : 0
      this.offsetLeft = this.$refs.slider.offsetLeft

      if(Array.isArray(this.value)) {
        this.numRange[0] = this.value[0]
        this.numRange[1] = this.value[1]
        this.left_1 = Math.ceil(this.numRange[0] * (100 / this.max))
        this.left_2 = Math.ceil(this.numRange[1] * (100 / this.max))
      }
      else {
        this.num = this.value
        this.left = Math.ceil(this.num * (100 / this.max))
      }
    },
    methods: {
      mousedown (event) {
        this.mouseStatus = 'down'
        this.show()
        if(this.range) {
          if(event.target.id == `${prefixClass}__pointLeft`) {
            this.mouseDir = 'left'
          }
          else {
            this.mouseDir = 'right'
          }
        }

        window.addEventListener('mousemove', this.mousemove);
        window.addEventListener('mouseup', this.mouseup);
      },
      mousemove (event) {
        if (this.mouseStatus === 'down') {
          this.positionLeft = event.x - this.offsetLeft
          this.changeLeft()
        }
      },
      mouseup (event) {
        if(this.mouseStatus === 'down') {
          this.mouseStatus = 'up'
          window.removeEventListener('mousemove', this.mousemove);
          window.removeEventListener('mouseup', this.mouseup);
        }
        this.hidden()
      },
      choose (event) {
        this.choosen = true
        this.positionLeft = event.x - this.offsetLeft
        this.changeLeft()
      },
      computedNum (left) {
        const unitNum = ((Number(this.max) - Number(this.min)) / Number(this.unit))
        let num = (unitNum * left / 100) * this.unit

        if(num % this.unit) {
          num = num - (num % this.unit) + this.unit
        }
        
        return num.toFixed(this.decimals)
      },
      changeLeft () {
        if(this.showPoint) {
          let left = this.positionLeft / this.clientWidth * 100
          const space = (100 / this.interval)
          const middleVal = space / 2
          const multiple = Math.floor(left / space)
          const remainder = left % space
          const numSpace = (Number(this.max) - Number(this.min)) / Number(this.interval)

          if(remainder < middleVal) {
            left = multiple * space
            this.num = multiple * numSpace
          }
          else {
            left = (multiple + 1) * space
            this.num = (multiple + 1) * numSpace
          }

          if(left > 100) left = 100
          if(left < 0) left = 0

          this.left = left
          this.$emit('input',this.num)
        }
        else if(this.range) {
          let left = this.positionLeft / this.clientWidth * 100
          let num = [0,0]

          if(left > 100) left = 100
          if(left < 0) left = 0

          if(this.choosen) {
            if(Math.abs(left - this.left_1) <= Math.abs(left - this.left_2)) {
              this.left_1 = left
            }
            else {
              this.left_2 = left
            }
            this.choosen = false
          }
          else {
            if(this.mouseDir === 'left') {
              this.left_1 = left
            }
            else {
              this.left_2 = left
            }         
          }
          num[0] = this.computedNum(this.left_1)
          num[1] = this.computedNum(this.left_2)
          num.sort()
          this.numRange[0] = num[0]
          this.numRange[1] = num[1]
          this.$emit('input',[num[0],num[1]])
        }
        else {
          let left = this.min

          if(this.clientWidth) {
            left = this.positionLeft / this.clientWidth * 100
          }

          if(left < 0) left = 0
          if(left > 100) left = 100

          this.left = left
          this.num = this.computedNum(left)
          this.$emit('input',this.num)
        }
      },
      handleClick(item) {
        this.num = item
        this.$emit('input',item)
      },
      handelBlur () {
        if(!Array.isArray(this.value)) {
          if(this.num < this.min) {
            this.num = this.min
          }
          if(this.num > this.max) {
            this.num = this.max
          }
          this.left = Math.ceil(this.num * (100 / this.max))
        }
        else {
          if(this.numRange[0] < this.min) {
            this.numRange[0] = this.min
          }
          if(this.numRange[0] > this.numRange[1]) {
            const item = this.numRange[0]
            this.numRange[0] = this.numRange[1]
            this.numRange[1] = item
          }
          this.left_1 = Math.ceil(this.numRange[0] * (100 / this.max))

          if(this.numRange[1] < this.numRange[0]) {
            const item = this.numRange[1]
            this.numRange[1] = this.numRange[0]
            this.numRange[0] = item
          }
          if(this.numRange[1] > this.max) {
            this.numRange[1] = this.max
          }
          this.left_2 = Math.ceil(this.numRange[1] * (100 / this.max))
        }
      },
      show () {
        const tooltip = this.$refs.line.children[1].children[1]
        tooltip.style.opacity = 1
        tooltip.style.visibility = 'visible'
      },
      hidden () {
        const tooltip = this.$refs.line.children[1].children[1]
        tooltip.style.opacity = 0
        tooltip.style.visibility = 'hidden'
      }
    }
  }
</script>