<template>
  <div :class="[`${prefix}-step`, `is-${currentStatus}`]"
    :style="style">
  <div :class="[`${prefix}-step__container`,
    `is-${currentStatus}`]">
    
    <div :class="`${prefix}-step__tail`"></div>

    <div :class="`${prefix}-step__icon`">
      <div v-if="showIcon" 
        :class="`${prefix}-step__icon-inner`">{{ index + 1 }}</div>
      <div v-else :class="iconClasses"></div>
    </div>

    <div :class="`${prefix}-step__main`">
      <div :class="[`${prefix}-step__title`, `is-${ currentStatus }`]">
        <slot name="title">{{ title }}</slot>
      </div>
      <div :class="[`${prefix}-step__description`, `is-${ currentStatus }`]">
        <slot name="description">{{ description }}</slot>
      </div>
    </div>

  </div>

  </div>
</template>

<script>
import { prefix } from '../../utils';

const prefixClass = prefix + '-step'

export default {
  name: prefixClass,
  props: {
    status: String,
    title: String,
    description: String,
  },
  data() {
    return {
      prefix,
      prefixClass,
      index: -1,
      internalStatus: '',
      style: {},
    }
  },
  computed:{
    customClass () {
      let customClass = prefixClass + ' '
      
      return customClass
    },
    showIcon() {
      return !['finish', 'error'].includes(this.currentStatus);
    },
    currentStatus() {
      return this.status || this.internalStatus;
    },
    stepsCount() {
      return this.$parent.steps.length;
    },
    prevStatus() {
      const prevStep = this.$parent.steps[this.index - 1];
      return prevStep ? prevStep.currentStatus : 'wait';
    },
    iconClasses() {
      let icon = '';

      if (this.currentStatus == 'finish') {
        icon = `${prefix}-icon ${prefix}-icon-wancheng`;
      } else if (this.currentStatus == 'error'){
        icon = `${prefix}-icon ${prefix}-icon-close`;
      }

      return [
        `${prefixClass}__icon-inner`,
        icon
      ]
    }
  },
  created() {
    if (this.$parent.direction === 'horizontal') {
      const childrens = this.$parent.$slots.default;
      this.style = {
        width: 100 / childrens.length + '%'
      }
    }

    this.$parent.steps.push(this);
  },
  mounted() {
    const unwatch = this.$watch('index', val => {
      this.$watch('$parent.active', 
        this.updateStatus, {
          immediate: true
        });
      unwatch();
    })
  },
  beforeDestroy() {
    const steps = this.$parent.steps;
    const index = steps.indexOf(this);
    
    if (index >= 0) {
      steps.splice(index, 1);
    }
  },
  methods: {
    updateStatus(val) {
      if (val > this.index) {
        this.internalStatus = 'finish';
      } else if (val === this.index && this.prevStatus !== 'error') {
        this.internalStatus = this.$parent.status;
      } else {
        this.internalStatus = 'wait';
      }
    }
  }
}
</script>
