<template>
  <div :class="customClass" :style="{ 'width': widthPercent }">
    <slot></slot>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-carousel-item'

  export default {
    name: prefixClass,
    props: {},
    data () {
      return {
        prefixClass,
        widthPercent: 0,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    created() {
      const childrens = this.$parent.$slots.default

      this.widthPercent = 100 / childrens.length + '%'
    },
    mounted () {
    },
    methods: {
    }
  }
</script>