<template>
  <li :class="customClass" @click="choose">
    <slot></slot>
  </li>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-dropdown-item'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number],
        required: true,
      }
    },
    data () {
      return {
        prefixClass,
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.$parent.value === this.value) {
          customClass += 'active '
        }
        
        return customClass
      }
    },
    methods: {
      choose () {
        this.$emit('change', this.value)
        this.$emit('input', this.value)

        this.$parent.$emit('change', this.value)
        this.$parent.$emit('input', this.value)
      }
    }
  }
</script>