<template>
  <div :class="customClass">
    <div :class="wrapperClass"  @click.stop="changeFocus">
      <div :class="[`${prefixClass}__wrapper-container`]">
        <div :class="[`${prefix}-cascader__tags `]" v-if="!(this.tags.length === 0)">
          <cd-tag v-for="(item,index) in tags" 
          :key="index" 
          closable 
          :size="size" 
          @close="handleClose(item)">
            {{item}}
          </cd-tag>
        </div>   

        <div :class="[`${prefixClass}-input `]" v-else>
          <input type="text" :class="[`${prefixClass}__inner `]" v-model="innerValue" placeholder="" >
        </div>
      </div>

      <span :class="[`${prefixClass}__wrapper-icon`]">
        <i :class="[`${prefix}-icon `,  `${prefix}-icon-top `]"></i>
      </span>
    </div>

    <div :class="panelClass">
      <div :class="[`${prefixClass}__panel`]"
      :style="{width: list.length * 164 +'px'}" 
      ref="cascaderItem">

        <div :class="[`${prefixClass}__panel-menu`]" 
        v-for="(item,index) in list.length" 
        :key="index" 
        v-show="show(item)"
        :style="{ width: width + '%' }">
          <ul :class="[`${prefixClass}__option-list `]">
              <li v-for="(i,index) in list[item-1].children" 
              :key="index"
              :class="liClass(i)"
              @click.stop="handleClick(i,item-1)">

              <span :class="[`${prefixClass}__option-list-item-inner `]">{{i.label}}</span>
              <i :class="[`${prefix}-icon `,`${prefix}-icon-left `]" v-if="showLeft(i)"></i> 

              </li>
          </ul>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-cascader'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Array],
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      size: {
        type: String,
        default: 'medium',  // large | medium | mini
        validator: function(value) {
          return ['large','medium','mini'].includes(value)
        }
      },
      multiple: {
        type: Boolean,
				default: false
      },
      options: {
        type:  Array,
        default: []
      }
    },
    data () {
      return {
        prefix,
        prefixClass,
        isFocus: false,
        innerValue: '',
        list:[],   //
        depth: 0,
        totalValue: this.multiple ? [] : this.value,   //value
        totalLabel: [],   //label
        tags: [],
        multipleValue:  this.multiple ? this.value : [],   //
        width: 0,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        customClass += `${prefixClass}__${this.size} `

        return customClass
      },
      wrapperClass () {
        let wrapperClass = prefixClass + '__wrapper '

        if (this.isFocus) {
          wrapperClass += `${prefixClass}__wrapper__focus `
        }
        if (this.disabled) {
          wrapperClass += `${prefixClass}__wrapper__disabled `
        }

        return wrapperClass
      },
      liClass () {
        return function (item) {
          let liClass = prefixClass + '__option-list-item '

          if (item.isLeaf) {
            liClass += `${prefixClass}__option-list-item-leaf `
          }

          if (item.disabled) {
            liClass += `${prefixClass}__option-list-item__disabled `
          }

          const arr = this.multiple ? this.getActiveItemList() : this.totalValue
          liClass = this.selectActiveByLi(liClass, item, arr)

          liClass += `${prefixClass}__option-list-item__${this.size} `

          return liClass
        }
      },
      panelClass () {
        let panelClass = prefixClass + '__panel-container '

        if (this.isFocus) {
          panelClass += `${prefixClass}__panel-container__focus `
        }

        return panelClass
      },
      show () {
        return function (item) {
          return this.list[item-1].children.length === 0 ? false : true
        }
      },
    },
    mounted () {
      const that = this
      document.addEventListener('click', function () {
        that.isFocus = false
      })

      this.getDeep(this.options,1)
      this.insertLeafNode(this.options)
      this.initList()
      this.getLabelByValue()
      this.width = 100 / this.depth
    },
    methods: {
      initList () {
        this.list = []
        for (let i = 0; i < this.depth; i++) {
          this.list.push({children:[]})
        }
      },
      changeFocus () {
        this.isFocus = !this.isFocus
      },
      getDeep (data, level) {
        data.forEach(item => {
          item.level = level
          if (level > this.depth) {
            this.depth = level
          }
          if ('children' in item){
            if (item.children.length > 0) {
              this.getDeep(item.children, level + 1)
            }
          }
        })    
      },
      insertLeafNode (data) {
        data.forEach(item => {
          item.isLeaf = item.children ? false : true
          item.disabled = item.disabled ? item.disabled : false
          if ('children' in item){
            if (item.children.length > 0) {
              this.insertLeafNode(item.children)
            }
          }
        })    
      },
      handleClick (i,level) {
        this.totalValue[level] = i.value
        this.totalValue = this.totalValue.slice(0,level+1)
        this.totalLabel[level] = i.label
        this.totalLabel = this.totalLabel.slice(0,level+1)

        if (!i.isLeaf) {
          this.showData()
          return
        }

        if (this.multiple) {
          this.handleMultipleClick(i,level)
        } else {
          this.handleSingleClick(i,level)
        }
      },
      handleMultipleClick () {
        const string = this.totalLabel.join('/')
          const index = this.tags.indexOf(string)
          if (index > -1) {
            this.tags.splice(index,1)
            this.handleSplice()
          } else {
            this.tags.push(string)
          }

          this.getValueByLabel()
          this.$emit('input',this.multipleValue)
          this.changeFocus()
      },
      handleSingleClick () {
        this.innerValue = this.totalLabel.join('/')
        const string = this.totalValue.join('/')
        this.$emit('input',string)
        this.changeFocus()
      },
      showData () {
        this.initList()
        this.options.forEach(item => {
          this.insertNodeToList(0, item)
        })

        this.insertData()
      },
      insertNodeToList (index, item) {
        let node = {}
        node.value = item.value
        node.label = item.label
        node.isLeaf = item.isLeaf
        node.disabled = item.disabled
        this.list[index].children.push(node)
      },
      insertData () {
        let option = this.options

        for (let i = 0; i < this.totalValue.length; i++) {
          let currentValue = this.totalValue[i]
          for (let index in option) {
            const { value, label } = option[index]
            if ([value, label].includes(currentValue)){
              currentValue = index
              break 
            }
          }
          
          if (option[currentValue] && option[currentValue].children) {
            option = option[currentValue].children
            option.forEach(item => {
              this.insertNodeToList(option[0].level-1, item)
            })
          }
        }
      },
      showLeft (i) {
        return  (i.isLeaf || i.disabled) ? false :true
      },
      getLabelByValue () {
        if (this.multiple) {
          this.multipleValue.forEach(string => {
            this.totalValue = string.split('/')
            let label = []
            this.showData()
            for (let i = 0; i < this.totalValue.length; i++) {
              this.list[i].children.forEach(item => {
                if (item.value === this.totalValue[i]) {
                  label[i] = item.label
                  if (item.isLeaf) {
                    this.totalLabel = label.join('/')
                    this.tags.push(this.totalLabel)
                  }
                }
              })
            }
          })
          this.handleSplice()
          this.getValueByLabel()
          this.$emit('input',this.multipleValue)
        } else {
          this.showData()
          for (let i = 0; i < this.totalValue.length; i++) {
            this.list[i].children.forEach(item => {
              if (item.value === this.totalValue[i]) {
                this.totalLabel[i] = item.label
                if (item.isLeaf) {
                  this.innerValue = this.totalLabel.join('/')
                }
              }
            })
          }
        }
      },
      getValueByLabel () {
        if (!this.multiple) return

        this.multipleValue = []
        this.tags.forEach(string => {
          this.totalValue = string.split('/')
          let label = []
          this.showData()
          for (let i = 0; i < this.totalValue.length; i++) {
            this.list[i].children.forEach(item => {
              if (item.label === this.totalValue[i]) {
                label[i] = item.value
                if (item.isLeaf) {
                  const totalValue = label.join('/')
                  this.multipleValue.push(totalValue)
                }
              }
            })
          }
        })
        this.multipleValue = this.uniq(this.multipleValue)
      },
      handleClose (tag) {
        const index = this.tags.indexOf(tag)
        this.tags.splice(index, 1);
        this.handleSplice()
        this.getValueByLabel()
        this.$emit('input', this.multipleValue)
      },
      uniq (arr) {
        let result = []
        arr.forEach(item => {
          if (!result.includes(item)) {
            result.push(item)
          }
        })
        return result
      },
      selectActiveByLi (liClass, item, arr) {
        const value = this.multiple ? item.label : item.value
        if (arr.includes(value)) {
          liClass += `active `
        }

        return liClass
      },
      getActiveItemList () {
        let activeItemList = []
          this.tags.forEach(tag => {
            const arr = tag.split('/')
            arr.forEach(i => {
              activeItemList.push(i)
            })
          })
          activeItemList = this.uniq(activeItemList)
          return activeItemList
      },
      handleSplice () {
        if (this.tags.length !== 0) {
          this.totalValue = []
          this.totalValue = this.multipleValue[this.multipleValue.length-2].split('/')
          this.totalLabel = this.tags[this.tags.length-1].split('/')
        }
      }
    }
  }
</script>