<template>
    <li :class="customClass" @click.stop="handleClick" v-show="isSearch">
      <span><slot></slot></span>
      <i :class="[`${prefix}-icon `,`${prefix}-icon-duihao `]" v-if="isActive"></i>
    </li>
</template>

<script>
  import { prefix } from '../../utils'
  const prefixClass = prefix + '-option'

  export default {
    name: prefixClass,
    props: {
      value: {
        type:[String,Number],
        default:'',
      },
      disabled: {
        type:Boolean,
        default:false,
      },    
    },
    data () {
      return {
        prefix,
        prefixClass,
        label: this.$slots.default[0].text,
      };
    }, 
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if(this.disabled) {
          customClass += prefixClass + '__disabled '
        }

        if(this.groupParent) {
          customClass += prefixClass + '__group '
        }

        if(this.isActive) {
          customClass += prefixClass + '__active '
        }

        switch(this.selectParent.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break;
          case 'medium':
            customClass += `${prefixClass}__medium `
            break;
          case 'mini':
            customClass += `${prefixClass}__mini `
            break;
        }

        return customClass
      },
      isSearcheResult () {
        const searchString = this.selectParent.innerValue.trim().toLowerCase()

        if(!searchString){
          return true;
        }

        if(this.label.toLowerCase().indexOf(searchString) !== -1) {
          return true
        }

        return false
      },
      isSearch () {
        const type = this.selectParent.type

        if(type === 'search') {
          return this.isSearcheResult
        }     

        return true
      },
      groupParent () {
        let parent = this.$parent;

        while (parent && parent.$options.name !== `${prefix}-option-group` ) {
          parent = parent.$parent;
        }

        return parent;
      },
      selectParent () {
        let parent = this.$parent;

        while (parent && parent.$options.name !== `${prefix}-select`) {
            parent = parent.$parent;
        }
        
        return parent;
      },
      isActive () {
        const parent = this.selectParent

        if(Array.isArray(parent.value)) {
          const parentValue = parent.value
          for(let i = 0; i < parentValue.length; i++) {
            if(parentValue[i] === this.value) {
              return true
            }
          }           
        }

        return this.value === parent.value
      },
    },
    created () {
      const parent = this.selectParent

      if(Array.isArray(parent.value)) {
        for(let i = 0; i<parent.value.length; i++) {
          if(parent.value[i] === this.value) {
            parent.list.push(this.label)
          }
        } 
      }
      else if(this.value === parent.value){
        parent.setLabel(this.label)
      }

    },  
    methods: {
      handleClick () {
        const parent = this.selectParent
        if (parent.multiple) {
          const valueIndex = parent.value.indexOf(this.value)
          const listIndex = parent.list.indexOf(this.label)

          if (valueIndex > -1) {
            parent.value.splice(valueIndex, 1)
          }
          else {
            parent.value.push(this.value)
          }

          if (listIndex > -1) {
            parent.list.splice(listIndex, 1)
          }
          else {
            parent.list.push(this.label)
          }
          
          parent.$emit('input', parent.value)
        }
        else {
          parent.isFocus = false
          parent.setLabel(this.label)
          parent.$emit('input', this.value)
        }
      },
    }
  }
</script>