<template>
  <div :class="customClass">
    <slot></slot>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-checkbox-group'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: Array,
        default: function() {
          return []
        }
      },
      disabled: {
        type: Boolean,
        default: false,
      }
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
    }
  }
</script>