<template>
  <div :class="customClass">
    <input type="text" :value="value"
      @change="handleChange" :disabled="disabled"
      @keyup.38="addValue"
      @keyup.40="reduceValue" />
    
    <div :class="addClass" @click="addValue">
      <i class="cds-icon cds-icon-top"></i>
    </div>
    <div :class="reduceClass" @click="reduceValue">
      <i class="cds-icon cds-icon-top"></i>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-input-number'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: Number,
        required: true
      },
      size: {
        type: String,
        default: 'medium', // large medium mini
      },
      disabled: {
        type: Boolean,
        default: false
      },
      max: {
        type: Number,
      },
      min: {
        type: Number,
      },
      unit: {
        type: Number,
        default: 1
      }
    },
    data() {
      return {
        prefix,
      }
    },
    computed: {
      customClass() {
        let customClass = prefixClass + ' '

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break;
          case 'mini':
            customClass += `${prefixClass}__mini `
            break;
        }

        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        return customClass
      },
      addClass() {
        return {
          [prefixClass + '__add']: true,
          'is-disabled': this.max !== undefined && this.value === this.max
        }
      },
      reduceClass() {
        return {
          [prefixClass + '__reduce']: true,
          'is-disabled': this.min !== undefined && this.value === this.min
        }
      },
    },
    methods: {
      handleChange() {
        this.$emit('input', this.value)
      },
      addValue() {
        let value = this.value + this.unit
        value = this.max !== undefined ? Math.min(value, this.max) : value

        this.$emit('input', value)
      },
      reduceValue() {
        let value = this.value - this.unit
        value = this.min !== undefined ? Math.max(value, this.min) : value

        this.$emit('input', value)
      },
    }
  }
</script>