<template>
  <form :class="customClass" @submit="handleSubmit">
    <slot />
  </form>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-form'

  const funcName = {
    required: 'isEmpty',
    date: 'isDate',
    boolean: 'isBoolean',
    email: 'isEmail',
    telephone: 'isMobilePhone',
    number: 'isNumeric',
    url: 'isURL',
  }

  // mode
  export default {
    name: prefixClass,
    props: {
      'label-width': {
        type: String,
        default: '80px'
      },
      'label-position': {
        type: String,
      },
      disabled: {
        type: Boolean,
        default: false
      },
      mode: {
        type: String, // inline() || vertical()
        default: 'vertical'
      },
      rules: {
        type: Object,
        default: function() {
          return {}
        }
      },
      model: {
        type: Object,
        default: function() {
          return {}
        }
      }
    },
    data () {
      return {
        prefixClass,
        childrens: {}
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        switch (this.mode) {
          case 'vertical':
            customClass += `${prefixClass}__vertical `
            break
          case 'inline':
            customClass += `${prefixClass}__inline `
            break
        }

        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        return customClass
      }
    },
    methods: {
      handleSubmit(event) {
        event.preventDefault()
        this.$emit('submit', event)

        return false
      },
      getChildren(prop) {
        return this.$children.find(children => children.prop === prop)
      },
      validate(callback) {
        const data = {}

        for (const prop in this.rules) {
          const rules = this.rules[prop]
          const children = this.getChildren(prop)

          if (!children) continue

          const message = children.validate(true)

          if (message) {
            data[prop] = children.message
          } else {
            Reflect.deleteProperty(data, prop)
          }
        }

        callback(Object.keys(data).length)
      },
    }
  }
</script>