<template>
  <div :class="[customClass, 'clearfix']">
    <div :class="[prefixClass + '__source']">
      <div :class="[prefixClass + '__header', 'clearfix']">
        <check-box :class="[prefixClass + '__checkall']" 
          :checked="sourceChecked.length > 0 && (sourceChecked.length === sourceList.length)"
          @change="chooseAllSource"></check-box>
        <span class="number">
          <span v-if="sourceChecked.length">{{ sourceChecked.length }}/</span>
          <span>{{ sourceList.length }}  </span>
        </span>
        <span :class="[prefixClass + '__title']">{{sourceTitle}}</span>
      </div>
      <div :class="[prefixClass + '__body']">
        <div :class="[prefixClass + '__search']">
          <input type="text" placeholder="" @input="filterSource" />
          <i class="cds-icon cds-icon-sousuo1 icon"></i>
        </div>
        <div :class="[prefixClass + '__list']">
          <div v-for="(item, index) in sourceList" v-show="!item.isHidden" :key="item.id">
            <check-box 
              :disabled="item.disabled"
              :checked="isSourceChecked(item.id)" 
              v-model="item.id" 
              @change="(active) => toggleChoose(active, 'source', item)">
              {{ item.content }}
            </check-box>
          </div>
        </div>
      </div>
    </div>
    <div :class="[prefixClass + '__actions']">
      <div class="source-to-target" 
        :class="[{ 'active': sourceChecked.length > 0 }]"
        @click="moveSourceToTarget"
      >
        <i class="cds-icon cds-icon-left"></i>
      </div>
      <div class="target-to-source" 
        :class="[{ 'active': targetChecked.length > 0 }]"
        @click="moveTargetToSource"
      >
        <i class="cds-icon cds-icon-left"></i>
      </div>
    </div>
    <div :class="[prefixClass + '__target']">
      <div :class="[prefixClass + '__header', 'clearfix']">
        <check-box :class="[prefixClass + '__checkall']" 
          :checked="targetChecked.length > 0 && (targetChecked.length === targetList.length)"
          @change="chooseAllTarget"></check-box>
        <span class="number">
          <span v-if="targetChecked.length">{{ targetChecked.length }}/</span>
          <span>{{targetList.length}} </span>
        </span>
        <span :class="[prefixClass + '__title']">{{targetTitle}}</span>
      </div>
      <div :class="[prefixClass + '__body']">
        <div :class="[prefixClass + '__search']">
          <input type="text" placeholder="" @input="filterTarget">
          <i class="cds-icon cds-icon-sousuo1 icon"></i>
        </div>
        <div :class="[prefixClass + '__list']">
          <div v-for="(item, index) in targetList" v-show="!item.isHidden" :key="item.id">
            <check-box 
              :disabled="item.disabled" 
              :checked="isTargetChecked(item.id)"
              v-model="item.id" 
              @change="(active) => toggleChoose(active, 'target', item)">
              {{ item.content }}
            </check-box>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  import CheckBox from '../checkbox'

  const prefixClass = prefix + '-transfer'

  export default {
    name: prefixClass,
    components: {
      CheckBox
    },
    props: {
      value: {
        type: Array,
        validator: function(value) {
          if (value.length !== 2) return false

          return true
        }
      }
    },
    data () {
      return {
        prefixClass,
        sourceTitle: this.value[0].title,
        sourceList: this.value[0].list,
        targetTitle: this.value[1].title,
        targetList: this.value[1].list,
        sourceChecked: [],
        targetChecked: [],
        sourceTime: null,
        targetTime: null,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
      toggleChoose (active, type, item) {
        if (active) {
          this[`${type}Checked`].push(item)
        } else {
          const index = this[`${type}Checked`].findIndex(data => data.id === item.id)

          index > -1 && this[`${type}Checked`].splice(index, 1)
        }
      },
      moveSourceToTarget () {
        if (this.sourceChecked.length === 0) return
        
        const sourceObject = this.arrayToObject(this.sourceList, 'id')
        // move source to target
        this.targetList.push(...this.sourceChecked)
        // clear source
        this.sourceChecked.forEach(source => {
          this.sourceList[sourceObject[source.id]] = null
        })
        this.sourceList = this.sourceList.filter(source => !!source)
        this.sourceChecked = []

        this.emitInput()
      },
      moveTargetToSource () {
        if (this.targetChecked.length === 0) return

        const targetObject = this.arrayToObject(this.targetList, 'id')
        // move target to source
        this.sourceList.push(...this.targetChecked)
        // clear target
        this.targetChecked.forEach(target => {
          this.targetList[targetObject[target.id]] = null
        })
        this.targetList = this.targetList.filter(target => !!target)
        this.targetChecked = []

        this.emitInput()
      },
      arrayToObject (arr, key) {
        const obj = {}

        arr.forEach((item, index) => {
          obj[item[key]] = index
        })

        return obj
      },
      emitInput () {
        this.$emit('input', [
          { title: this.sourceTitle, list: this.sourceList },
          { title: this.targetTitle, list: this.targetList },
        ])
      },
      filterSource (event) {
        if (this.sourceTime) {
          clearTimeout(this.sourceTime)
        }

        this.sourceTime = setTimeout(() => {
          const value = event.target.value

          this.sourceList = this.sourceList.map(source => {
            if (!value.trim()) {
              source.isHidden = false
            } else {
              const re = new RegExp(value)
              source.isHidden = !re.test(source.content)
            }

            return source
          })
          this.sourceTime = null
        }, 300)
      },
      filterTarget (event) {
        if (this.targetTime) {
          clearTimeout(this.targetTime)
        }
        
        this.targetTime = setTimeout(() => {
          const value = event.target.value

          this.targetList = this.targetList.map(target => {
            if (!value.trim()) {
              target.isHidden = false
            } else {
              const re = new RegExp(value)
              target.isHidden = !re.test(target.content)
            }

            return target
          })
          this.targetTime = null
        }, 300)
      },
      chooseAllSource (active) {
        this.sourceChecked = []

        if (active) {
          this.sourceChecked.push(...this.sourceList)
        }
      },
      chooseAllTarget (active) {
        this.targetChecked = []

        if (active) {
          this.targetChecked.push(...this.targetList)
        }
      },
      isSourceChecked (id) {
        const index = this.sourceChecked.findIndex(item => item.id === id)
        
        return index > -1
      },
      isTargetChecked (id) {
        const index = this.targetChecked.findIndex(item => item.id === id)

        return index > -1
      }
    }
  }
</script>