<template>
  <div :class="customClass">
   <span ref="trigger" @mouseenter="show" @mouseleave="hidden">
     <slot></slot>
   </span>
    
    <div :class="[prefixClass + '__container']"
    :style="{top: position.top + 'px', left: position.left + 'px', opacity: opacity, visibility: visibility}"
    ref="tooltip">
      <div :class="[prefixClass + '__inner']">
        {{content}}
      </div>
      <div :class="[prefixClass + '__arrow']"></div>
    </div>
    
  </div>
</template>

<script>
import { prefix } from '../../utils'

const prefixClass = prefix + '-tooltip'

export default {
  name: prefixClass,
  props: {
    value: {
      type: [String,Number],
      default: 0 
    },
    placement: {
      type: String,
      default: 'top'    //top | bottom | left | right
    },
    location: {
      type: String,
      default: 'center' //center | border
    },
    content: {
      type: String,
      default: '' 
    },
    manual: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      prefixClass,
      position: {
        top: 0,
        left: 0,
      },
      opacity: Number(this.value),
    };
  },
  computed: {
    customClass () {
      let customClass = prefixClass + ' '
      
      switch(this.placement) {
        case 'top':
          customClass += `${prefixClass}__top ` 
          break;
        case 'bottom':
          customClass += `${prefixClass}__bottom ` 
          break;
        case 'left':
          customClass += `${prefixClass}__left ` 
          break;
        case 'right':
          customClass += `${prefixClass}__right ` 
          break;
      }

      switch(this.location) {
        case 'edge':
          customClass += `${prefixClass}__edge ` 
          break;
        case 'center':
          customClass += `${prefixClass}__center ` 
          break;
      }
      
      return customClass
    },
    visibility () {
      return this.opacity === 0 ? 'hidden' : 'visible'
    }
  },
  mounted () {
    this.computeTooltipPosition()
  },
  watch: {
    value () {
      this.opacity = Number(this.value)
    }
  },
  methods: {
    show () {
      if (!this.manual) {
        this.opacity = 1
      }
    },
    hidden () {
      if (!this.manual) {
        this.opacity = 0
      }
    },
    computeCenter (offsetLeft, offsetTop, clientWidth, clientHeight, tooltipWidth) {
      let top = 0
      let left = 0
      switch (this.placement) {
        case 'top' :
          top = offsetTop - 46
          left = offsetLeft + (clientWidth / 2)
          break
        case 'bottom' :
          top = clientHeight + offsetTop + 12 + 5
          left = offsetLeft + (clientWidth / 2)
          break
        case 'left' :
          top = offsetTop + (clientHeight / 2) + 5
          left = offsetLeft - (tooltipWidth + 12)
          break
        case 'right' :
          top = offsetTop + (clientHeight / 2) + 5
          left = offsetLeft + clientWidth + 12
          break
      }
      this.$set(this.position,'top',top)
      this.$set(this.position,'left',left)
    },
    computeEdge (offsetLeft, offsetTop, clientWidth, clientHeight, tooltipWidth) {
      let top = 0
      let left = 0
      switch (this.placement) {
        case 'top' :
          top = offsetTop - 46
          left = offsetLeft + (tooltipWidth / 2)
          break
        case 'bottom' :
          top = offsetTop + clientHeight + 12 + 5
          left = offsetLeft + (tooltipWidth / 2)
          break
        case 'left' :
          top = offsetTop + (clientHeight / 2) + 6
          left = offsetLeft - (tooltipWidth + 16)
          break
        case 'right' :
          top = offsetTop + (clientHeight / 2) + 6
          left = offsetLeft + clientWidth + 16
          break
      }
      this.$set(this.position, 'top', top)
      this.$set(this.position, 'left', left)
    },
    computeTooltipPosition () {
      const trigger = this.$refs.trigger.children[0]
      const tooltip = this.$refs.tooltip
      const offsetLeft = trigger.offsetLeft
      const offsetTop = trigger.offsetTop
      const clientWidth = trigger.clientWidth + 2
      const clientHeight = trigger.clientHeight + 2
      const tooltipWidth = tooltip.clientWidth

      if (this.location === 'center') {
        this.computeCenter(offsetLeft, offsetTop, clientWidth, clientHeight, tooltipWidth)
      } else if (this.location === 'edge') {
        this.computeEdge(offsetLeft, offsetTop, clientWidth, clientHeight, tooltipWidth)
      }
    }
  }
}
</script>