<template>
  <transition
    name="dialog-fade"
    @after-enter="afterEnter"
    @after-leave="afterLeave">
    <div 
      v-show="visible" 
      :class="[prefixClass + '__container']"
    >
      <div :class="[prefixClass + '__shadow']"></div>
      <div :class="customClass" :style="style">
        <div v-if="!noHeader" :class="[prefixClass + '__header']">
          <slot name="title">
            <div :class="[prefixClass + '__title']">{{title}}</div>
          </slot>
          <div :class="[prefixClass + '__close']">
            <i class="cds-icon cds-icon-guanbi" @click="handleClose"></i>
          </div>
        </div>
        
        <div :class="[prefixClass + '__body']">
          <slot></slot>
        </div>
    
        <div :class="[prefixClass + '__footer']">
          <slot name="footer">
            <cd-button size="medium" @click="handleClose"></cd-button>
            <cd-button type="primary" size="medium"></cd-button>
          </slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import { prefix } from '../../utils'

  import CdButton from '../button'

  const prefixClass = prefix + '-dialog'

  export default {
    name: prefixClass,
    components: {
      CdButton
    },
    props: {
      title: String,
      'no-header': Boolean,
      beforeClose: Function,
      top: {
        type: String,
        default: '15vh',
      },
      width: String,
      fullscreen: {
        type: Boolean,
        default: false,
      },
      visible: {
        type: Boolean,
        default: false,
      }
    },
    data () {
      return {
        prefixClass
      }
    },
    watch: {
      visible (value) {
        if (value) {
          this.$emit('open')
        } else {
          this.$emit('close')
        }
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      },
      style() {
        let style = {};
        if (!this.fullscreen) {
          style.marginTop = this.top;
          if (this.width) {
            style.width = this.width;
          }
        }
        return style;
      }
    },
    methods: {
      afterEnter () {
        this.$emit('opened')
      },
      afterLeave () {
        this.$emit('closed')
      },
      handleClose() {
        if (typeof this.beforeClose === 'function') {
          this.beforeClose(this.hide)
        } else {
          this.hide()
        }
      },
      hide() {
        this.$emit('update:visible', false)
        this.$emit('close')
      },
    }
  }
</script>