<template>
  <ul :class="customClass" ref="menu">
    <slot></slot>
  </ul>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-dropdown-menu'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String, Number],
        required: true
      }
    },
    data () {
      return {
        prefixClass
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    methods: {
    }
  }
</script>