<template>
  <transition
    name="drawer-transition"
    @after-enter="drawerOpened"
    @after-leave="drawerClosed"
  >
    <div :class="customClass" v-show="visible">
      <div :class="[prefixClass + '__shadow']" @click="closeDrawer"></div>
      <div :class="[prefixClass + '__container', direction]" :style="style">
        <div :class="[prefixClass + '__header']">
          <slot name="header"></slot>
        </div>
        <div :class="[prefixClass + '__content']">
          <slot name="content"></slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-drawer'

  export default {
    name: prefixClass,
    props: {
      direction: {
        type: String,
        validator: function(value) {
          return['ltr', 'rtl', 'ttb', 'btt'].indexOf(value) > -1
        }
      },
      percent: {
        type: String,
        default: '30%'
      },
      visible: {
        type: Boolean,
        default: false
      },
    },
    data () {
      return {
        prefixClass,
        style: {},
      };
    },
    watch: {
      percent: {
        immediate: true,
        handler (value) {
          if (['ltr', 'rtl'].indexOf(this.direction) > -1) {
            this.style = { width: value }
          } else if (['ttb', 'btt'].indexOf(this.direction) > -1) {
            this.style = { height: value }
          }
        }
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.visible) {
          customClass += `${this.prefixClass}__open `
        }

        return customClass
      }
    },
    methods: {
      drawerOpened () {
        this.$emit('opened')
      },
      drawerClosed () {
        this.$emit('closed');
      },
      closeDrawer () {
        this.$emit('update:visible', false)
      }
    }
  }
</script>