<template>
  <div :class="prefixClass" :data-time="innerValue">
    <div :class="prefixClass + '__current'">
      <input type="text" :value="hoverTime" readonly />
    </div>
    <div :class="prefixClass + '__time'">
      <div :class="prefixClass + '__hour'">
        <div v-for="(item, index) in new Array(24)"
          :class="['time-item', { 'active': parseInt(hour) == index }]"
          @click="hour = index" 
          @mouseover="hourPreview = index"
          @mouseout="hourPreview = hour"
          :key="index">
          {{ index < 10 ? `0${index}` : index }}
        </div>
      </div>
      <div :class="prefixClass + '__minute'">
        <div v-for="(item, index) in new Array(60)"
          :class="[ 'time-item', { 'active': parseInt(minute) == index }]" 
          @click="minute = index" 
          @mouseover="minutePreview = index"
          @mouseout="minutePreview = minute"
          :key="index">
          {{ index < 10 ? `0${index}` : index }}
        </div>
      </div>
      <div :class="prefixClass + '__second'">
        <div v-for="(item, index) in new Array(60)"
          :class="['time-item', { 'active': parseInt(second) == index }]"
          @click="second = index" 
          @mouseover="secondPreview = index"
          @mouseout="secondPreview = second"
          :key="index">
          {{ index < 10 ? `0${index}` : index }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-time'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: String,
        required: true
      }
    },
    data () {
      return {
        prefixClass,
        hour: '',
        hourPreview: '',
        minute: '',
        minutePreview: '',
        second: '',
        secondPreview: '',
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      },
      innerValue() {
        const hour = this.hour < 10 ? '0' + this.hour : this.hour
        const minute = this.minute < 10 ? '0' + this.minute : this.minute
        const second = this.second < 10 ? '0' + this.second : this.second
        const innerValue = `${hour}:${minute}:${second}`

        this.$emit('input', innerValue)

        return innerValue
      },
      hoverTime() {
        const hourPreview = this.hourPreview < 10 ? '0' + this.hourPreview : this.hourPreview
        const minutePreview = this.minutePreview < 10 ? '0' + this.minutePreview : this.minutePreview
        const secondPreview = this.secondPreview < 10 ? '0' + this.secondPreview : this.secondPreview

        return `${hourPreview}:${minutePreview}:${secondPreview}`
      }
    },
    created() {
      this.parseTime()
    },
    methods: {
      parseTime() {
        const arr = this.value.split(':').reverse()

        this.hour = arr[2] || ''
        this.minute = arr[1] || ''
        this.second = arr[0] || ''
      },
    }
  }
</script>