<template>
  <div :class="customClass" :v-model="currentPage">

    <ul :class="[ `${prefix}-pagination__pager-list `]">
      <li :class="[`${prefix}-pagination__pager-prev `, `${prefix}-pagination__pager `,{ [`${prefix}-pagination__pager-disabled `] : this.isFront }]" @click="handlePrev" >
        <i class="cds-icon cds-icon-top"></i>
      </li>

      <li   :class="[liClass, `${prefix}-pagination-item-1 `,{ [`${prefix}-pagination-item-active `] : current == 1 }]" :title="1" @click="handleClick(1)">
        <a >1</a>
      </li>
      <li :class="[liClass,' icon']" title="..."  @click="handleClick(current - 5)" v-show="this.foldFront">
        <i :class="[`${prefix}-icon `, `${prefix}-icon-icon-test `]"></i>
      </li>

      <li v-for="(item,index) of this.page" :key="index" :class="[liClass, `${prefix}-pagination-item-` + item,{ [` ${prefix}-pagination-item-active `] : current == item }]" :title="item" @click="handleClick(item)">
        <a >{{item}}</a>
      </li>

      <li :class="[liClass,' icon']" title="..."  @click="handleClick(current + 5)" v-show="this.foldLast">
        <i :class="[`${prefix}-icon `, `${prefix}-icon-icon-test `]"></i>
      </li>
      <li :class="[liClass, `${prefix}-pagination-item-` + this.pageNum, { [prefixClass + '-item-active']: current == pageNum }]" :title="this.pageNum" @click="handleClick(pageNum)">
        <a >{{this.pageNum}}</a>
      </li>

      <li :class="[`${prefix}-pagination__pager-next `, `${prefix}-pagination__pager `, { [`${prefix}-pagination__pager-disabled `] : this.isLast }]" @click="handleNext" >
        <i :class="[`${prefix}-icon `, `${prefix}-icon-top `]"></i>
      </li>
    </ul>

    <span :class="[`${prefix}-pagination__showQuickJumper `]" v-if="this.showQuickJumper">
      <input type="number" :class="prefixClass + '__showQuickJumper-inner'" @keydown.enter="submitPageNum" v-model="innerValue">
    </span>

    <span :class="[`${prefix}-pagination__showPageNum `]" v-if="this.showPageNum">
      {{this.currentPage}}/{{this.pageNum}}
    </span>

  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-pagination'

  export default {
    name: prefixClass,
    model: {
      prop: 'currentPage',
      event: 'change'
    },
    props: {
      value: {
        type: [Number,String],
        default: 1
      },
      currentPage: {
        type: [Number,String],
      },
      total: {
        type: [Number,String],
      },
      pageSize: {
        type: [Number,String],
        default: 10
      },
      showQuickJumper: {
        type: Boolean,
        default: false
      },
      showPageNum: {
        type: Boolean,
        default: false
      },
      simple: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        prefix,
        prefixClass,
        current: this.currentPage,
        innerValue: this.value,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        if(this.simple) {
          customClass += `${prefixClass}__simple `
        }
        
        return customClass
      },
      liClass () {
        let liClass = prefixClass + '__pager '

        liClass += `${prefixClass}-item `

        return liClass
      },
      pageNum () {
        let pageNum = Number(this.total) / Number(this.pageSize)
        return Math.ceil(pageNum)
      },
      isFront () {
        if(this.currentPage == 1) {
          return true
        }
        return false
      },
      isLast () {
        if(this.currentPage == this.pageNum) {
          return true
        }
        return false
      },
      page () {
        let page = []
        let start = 0

        if(this.pageNum > 7) {
          if(this.currentPage - 2 <= 2) {
            start = 2;
            for(let i = 0; i < 5 ; i++ ) {
              page[i] = start++
            }
          }
          else if(this.currentPage + 2 >= this.pageNum-1) {
            start = this.pageNum - 5;
            for(let i = 0; i < 5 ; i++ ) {
              page[i] = start++
            }
          }
          else {
            start = this.currentPage - 2
            for(let i = 0; i < 5 ; i++ ) {
              page[i] = start++
            }
          }
        }
        else {
          start = 2
          for(let i = 0; i < (this.pageNum - 2) ; i++ ) {
            page[i] = start++
          }
        }
        
        return page
      },
      foldFront () {
        if(this.currentPage - 2 <= 2) {
          return false
        }
        return true
      },
      foldLast () {
        if(this.currentPage + 2 >= this.pageNum-1) {
          return false
        }
        return true
      }
    },
    methods: {
      handleClick (item) {
        if(item < 1) {
          this.$emit('change',1)
        }
        else if (item > this.pageNum) {
          this.$emit('change',this.pageNum)
        }
        else {
          this.$emit('change',item)
        }
      },
      handlePrev () {
        if(this.currentPage > 1) {
          this.$emit('change',this.currentPage - 1)
        }
      },
      handleNext () {
        if (this.currentPage < this.pageNum) {
          this.$emit('change',this.currentPage + 1)
        }
      },
      submitPageNum () {
        this.innerValue = Number(this.innerValue)
        if(this.innerValue > this.pageNum) {
          this.innerValue = this.pageNum
        }
        if(this.innerValue < 1) {
          this.innerValue = 1
        }
        this.$emit('input',this.innerValue)
        this.$emit('change',this.innerValue)
      }
    },
    watch: {
      currentPage () {
        this.current = this.currentPage
      },
      value () {
        this.innerValue = this.value
      }
    }
  }
</script>