<template>
  <div :class="customClass" @click="handleClick">
    <upload-dragger @file="uploadFiles" v-if="drag"><slot></slot></upload-dragger>
    <template v-else-if="listType === 'picture-card'">
      <div class="upload-btn"><slot></slot></div>
    </template>
    <template v-else-if="listType === 'file'">
      <slot></slot>
    </template>
    <template v-else><slot></slot></template>
    <input ref="input" type="file" name="uploadfile" @change="handleChange" multiple="true" />
  </div>
