<script>
import {prefix} from '../../utils'
import ajax from "./ajax"
import UploadDragger from "./upload-dragger.vue"

const prefixClass = prefix + '-upload-container'

function noop() {}

export default {
  name: prefixClass,
  components: {UploadDragger},
  data() {
    return {
