<template>
  <div :class="customClass" ref="carousel">
    <div v-show="innerWidth"
      :class="[prefixClass + '__list']" 
      :style="{ 
        width: innerWidth * childrenLength + 'px', 
        transform: `translateX(-${currentIndex * innerWidth}px)` 
      }">
      <slot></slot>
    </div>

    <div v-if="pagination" :class="[prefixClass + '__pagination']">
      <div v-for="(item, index) in Array(childrenLength)"
        :class="[
          prefixClass + '__pagination-item',
          { active: currentIndex === index }
        ]"
        @click="currentIndex = index"
      ></div>
    </div>
    <div v-if="navigation || navigation.prev"  
      :class="[prefixClass + '__prev']"
      @click="prevItem"
    >
      <i :class="['cds-icon cds-icon-zuojiantou']"></i>
    </div>
    <div v-if="navigation || navigation.next" 
      :class="[prefixClass + '__next']"
      @click="nextItem"
    >
    <i :class="['cds-icon cds-icon-zuojiantou',]"></i>  
  </div>
    
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-carousel'

  export default {
    name: prefixClass,
    props: {
      trigger: {
        type: String,
        default: 'hover', // hover | click
      },
      pagination: Boolean,
      navigation: {
        type: [Boolean, Object],
        validator: function(value) {
          if (typeof value === 'boolean') return true
          
          return value.hasOwnProperty('prev') || value.hasOwnProperty('next')
        }
      },
    },
    data () {
      return {
        prefixClass,
        currentIndex: 0,
        innerWidth: 0,
        childrenLength: 0,
        paginationList: null,
        visibleNavigation: false,
        lastCalcTime: 0,
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    mounted () {
      this.childrenLength = this.$children.length
      this.calcWidth()
      window.addEventListener('resize', this.handleWindowResize)
    },
    beforeDestroy () {
      window.removeEventListener('resize', this.handleWindowResize)
    },
    methods: {
      prevItem () {
        if (this.currentIndex === 0) return

        this.currentIndex--
      },
      nextItem () {
        if (this.currentIndex === this.childrenLength - 1) return

        this.currentIndex++
      },
      handleWindowResize () {
        if (this.lastCalcTime) return

        this.lastCalcTime = setTimeout(() => {
          this.calcWidth()
          this.lastCalcTime = null
        }, 200)
      },
      calcWidth () {
        const $carousel = this.$refs.carousel

        if (!$carousel) return

        const style = window.getComputedStyle($carousel)

        this.innerWidth = parseFloat(style.width)
      }
    }
  }
</script>