<template>
  <div :class="customClass">
    <div v-if="$slots.left" :class="[prefixClass + '-group__left']">
      <slot name="left"></slot>
    </div>

    <textarea
      v-if="type === 'textarea'"
      v-model="innerValue"
      :class="[prefixClass + '__inner']"
      :disabled="disabled"
      :placeholder="placeholder"
      @change="handleChange"
      @input="handleInput"
      @blur="handleBlur"
      @focus="handleFocus"
    ></textarea>

    <input
      v-if="type !== 'textarea'"
      v-model="innerValue"
      ref="input"
      :class="[prefixClass + '__inner']"
      :disabled="disabled"
      :type="inputType" 
      :placeholder="placeholder"
      @change="handleChange"
      @input="handleInput"
      @blur="handleBlur"
      @focus="handleFocus"
    />

    <div v-if="$slots.right" :class="[prefixClass + '-group__right']">
      <slot name="right"></slot>
    </div>

    <i 
      v-if="clear && type !== 'password' && innerValue.length > 0"
      class="cds-icon cds-icon-qingchu"
      @click="handleClear"></i>
    
    <template v-if="type === 'password'">
      <i v-show="showPassword" 
        class="cds-icon cds-icon-mimaxianshi" 
        @click="showPassword = false"></i>
      <i v-show="!showPassword"
        class="cds-icon cds-icon-yincang" 
        @click="showPassword = true"></i>
    </template>

    <div 
      v-if="type === 'search'"
      v-show="isShowResult"
      :class="[prefixClass + '-search-result']">
      <div v-if="loading" :class="[prefixClass + '-search__loading']">
        <div class="cds-icon cds-icon-loading"></div>
        
      </div>
      <div v-else-if="searchResults.length === 0"
        class="empty"></div>
      <div v-else
        class="list-item"
        :class="{ 'is-current': item.value === innerValue }"
        v-for="item in searchResults"
        @click="chooseSearchResult(item)" 
        :key="item.id">{{ item[prop] }}</div>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils';
  
  const prefixClass = `${prefix}-input`;

	export default {
    name: prefixClass,
    props: {
      value: {
        type: String,
        required: true
      },
      type: {
        type: String, // default | textarea | search
        default: 'default',
      },
      clear: {
        type: Boolean,
        default: false,
      },
      size: {
        type: String,
        default: 'medium'
      },
      placeholder: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      prop: {
        type: String,
        default: 'value'
      },
      searchResults: {
        type: Array,
        default: function() {
          return []
        }
      },
      loading: {
        type: Boolean,
        default: false,
      }
    },
    watch: {
      value() {
        this.innerValue = this.value
      },
      searchResults() {
        if (Array.isArray(this.searchResults) && this.searchResults.length > 0) {
          this.isShowResult = true
        }
      }
    },
    data() {
      return {
        prefixClass,
        innerValue: this.value,
        showPassword: false,
        isShowResult: false,
      }
    },
    computed: {
      inputType() {
        let type = 'text'

        if (this.type === 'password') {
          type = this.showPassword ? 'text' : 'password'
        }

        return type
      },
      customClass() {
        let customClass = prefixClass + ' '

        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break
          case 'mini':
            customClass += `${prefixClass}__mini `
            break
        }

        switch (this.type) {
          case 'search': 
            customClass += `${prefixClass}__search `
            break
          case 'textarea':
            customClass += `${prefixClass}__textarea `
            break
        }

        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        if (this.clear) {
          customClass += 'is-clear '
        }

        if (this.$slots.left || this.$slots.right) {
          customClass += `${prefixClass}-group `
          if (this.$slots.left) {
            customClass += `${prefixClass}-group-left `
          }
          if (this.$slots.right) {
            customClass += `${prefixClass}-group-right `
          }
        }

        return customClass
      }
    },
    created() {
      if (this.type === 'search') {
        document.documentElement.addEventListener('click', this.hideSearchResults.bind(this))

        if (Array.isArray(this.searchResults) && this.searchResults.length > 0) {
          this.isShowResult = true
        }
      }
    },
    beforeDestroy() {
      if (this.type === 'search') {
        document.documentElement.removeEventListener('click', this.hideSearchResults.bind(this))
      }
    },
    methods: {
      handleChange() {
        this.$emit('change', this.innerValue)
      },
      handleInput() {
        this.$emit('input', this.innerValue)
      },
      handleBlur(event) {
        this.$emit('blur', this.innerValue)
      },
      handleFocus(event) {
        this.showSearchResults()
        this.$emit('focus', this.innerValue)
      },
      handleClear() {
        this.innerValue = ''
        this.$emit('input', this.innerValue)
      },
      chooseSearchResult(item) {
        this.innerValue = item.value
        this.$emit('choose', item)
      },
      showSearchResults() {
        this.isShowResult = true
      },
      hideSearchResults(event) {
        if (event.target == this.$refs.input) return

        this.isShowResult = false
      }
    }
  }
</script>