<template>
  <div :class="customClass">
    <slot></slot>
  </div>
</template>

<script>
  import { prefix } from '../../utils'
  const prefixClass = prefix + '-radio-group'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: [String,Number,Boolean],
        required: true,
      },
      disabled: {
				type: Boolean,
				default: false,
      },
    },
    data () {
      return {
        prefixClass,
      };
    },
    watch: {
      value(newValue) {
        this.$emit("input", newValue);
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        return customClass
      }
    },
    methods: {
    }
  }
</script>