<template>
  <ul :class="customClass" :label="label" >
    <li :class="[`${prefix}-option-group__title `]" @click="handleClick">
      <span>{{this.label}}</span>
    </li>
    <li>
      <ul :class="[`${prefix}-option-group-list `]" >
        <slot></slot>
      </ul>
    </li>
  </ul>
</template>

<script>
  import { prefix } from '../../utils'
  const prefixClass = prefix + '-option-group'

  export default {
    name: prefixClass,
    props: {
      label: {
        type: [String,Number,Boolean],
        default: ''
      },
    },
    data () {
      return {
        prefix,
        prefixClass,
      };
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        return customClass
      },
      selectParent () {
        let parent = this.$parent;

        while (parent && parent.$options.name !== `${prefix}-select` ) {
            parent = parent.$parent;
        }

        return parent;
      },
    },
    methods: {
      handleClick () {
        const parent = this.selectParent

        if(parent.multiple){
          let  newSelected = []

          for(let i = 0;i<this.$slots.default.length;i++){
            const value = this.$slots.default[i].child.value
            const label = this.$slots.default[i].child.label
            const disabled = this.$slots.default[i].child.disabled
            const valueIndex = parent.value.indexOf(value)
            const listIndex = parent.list.indexOf(label)
            if (!disabled) {
              if (valueIndex === -1) {
                parent.value.push(value)
                newSelected.push(value)
              }
              if (listIndex === -1) {
                parent.list.push(label)
              }
            }
          } 

          if(newSelected.length === 0){
            for(let i = 0;i<this.$slots.default.length;i++){
              const value = this.$slots.default[i].child.value
              const label = this.$slots.default[i].child.label
              const valueIndex = parent.value.indexOf(value)
              const listIndex = parent.list.indexOf(label)  
              
              if (valueIndex > -1 && listIndex > -1) {
                parent.value.splice(valueIndex,1)
                parent.list.splice(listIndex,1)
              }
            } 
          }
          
          parent.$emit('input',parent.value)    
          parent.$emit('change',parent.value) 
        }
      },
    }
  }
</script>