<template>
  <div :class="customClass" ref="datePickerRange">
    <input type="text" 
      :value="innerValue" 
      :disabled="disabled" 
      @focus="isShowDatePanel = true"
      readonly 
    />
    <i class="cds-icon cds-icon-riqi"></i>
    <div :class="prefixClass + '__panel'" v-show="isShowDatePanel">
      <div :class="prefixClass + '__current'">
        <div class="pull-left">
          <i class="cds-icon cds-icon-xiangshang prev-year"
            @click="changeDay('subtract', 'year')"></i>
          <i class="cds-icon cds-icon-left prev-month"
            @click="changeDay('subtract', 'month')"></i>
          <span style="cursor: pointer;margin-left: 54px;font-weight: bold;">
            <span>{{ startDay.$y }}</span> 
            <span>{{ startDay.$M + 1 }}</span>
          </span>
        </div>
        <div class="pull-right">
          <span style="cursor: pointer;margin-right: 54px;font-weight: bold;">
            <span>{{ endDay.$y }}</span> 
            <span>{{ endDay.$M + 1 }}</span>
          </span>
          <i class="cds-icon cds-icon-xiangshang next-year"
            @click="changeDay('add', 'year')"></i>
          <i class="cds-icon cds-icon-left next-month"
            @click="changeDay('add', 'month')"></i>
        </div>
      </div>
      <!--  -->
      <div style="overflow: auto;" v-if="type === 'daterange'">
        <cd-day 
          :minDate="minDate"
          :maxDate="maxDate"
          :year="startDay.$y"
          :month="startDay.$M"
          :chooseDay="chooseDay"
          @change="chooseCurrentDay" 
          style="float: left;"
        />
        <cd-day
          :minDate="minDate"
          :maxDate="maxDate"
          :year="endDay.$y"
          :month="endDay.$M"
          :chooseDay="chooseDay"
          @change="chooseCurrentDay" 
          style="float: left;"
        />
      </div>

      <div style="overflow: auto" v-if="type === 'monthrange'">
        <cd-month @change="chooseCurrentMonth" />
        <cd-month @change="chooseCurrentMonth" />
      </div>
    </div>
  </div>
</template>

<script>
  import dayjs from 'dayjs'

  import { prefix } from '../../utils'
  import CdDay from '../date-picker/day.vue'
  import CdMonth from '../date-picker/month.vue'
  import CdYear from '../date-picker/year.vue'

  const prefixClass = prefix + '-date-picker-range'

  export default {
    name: prefixClass,
    components: {
      CdDay,
      CdMonth,
      CdYear,
    },
    props: {
      value: {
        type: Array,
        required: true,
      },
      type: {
        type: String, // daterange || monthrange
        default: 'daterange'
      },
      size: {
        type: String,
        default: 'medium'
      },
      disabled: {
        type: Boolean,
        default: false
      },
      minDate: {
        type: [String, Object],
        validator: function(value) {
          if (value === undefined) return true

          if (!(value instanceof dayjs)) {
            return dayjs(value).isValid()
          }

          return true
        }
      },
      maxDate: {
        type: [String, Object],
        validator: function(value) {
          if (value === undefined) return true

          if (!(value instanceof dayjs)) {
            return dayjs(value).isValid()
          }

          return true
        }
      },
      placeholder: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        prefixClass,
        currentView: this.startView,
        startDay: dayjs(),
        endDay: dayjs().add(1, 'month'),
        chooseDay: [],
        currentDay: {},
        isShowDatePanel: false,
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        switch (this.size) {
          case 'large':
            customClass += `${prefixClass}__large `
            break
          case 'medium':
            customClass += `${prefixClass}__medium `
            break
          case 'mini':
            customClass += `${prefixClass}__mini `
            break
        }

        if (this.disabled) {
          customClass += `${prefixClass}__disabled `
        }

        return customClass
      },
      innerValue() {
        if (this.chooseDay.length === 0) {
          this.$emit('input', [])
          return ''
        }

        const {$y, $M, $D} = this.chooseDay[0]
        const {$y: $y1, $M: $M1, $D: $D1} = this.chooseDay[1] || {}
        let startDate = `${$y}-${$M + 1}-${$D}`
        let endDate = `${$y1}-${$M1 + 1}-${$D1}`

        if (!$y1) {
          this.$emit('input', [startDate])
          return startDate + ' ~ '
        }

        this.$emit('input', [startDate, endDate])
        return `${startDate} ~ ${endDate}`
      }
    },
    created() {
      if (this.startView > this.endView) {
        throw new Error('startView can not be greater than endView')
      }

      this.initData()
      window.addEventListener('click', this.hideDatePanel.bind(this))
    },
    beforeDestroy() {
      window.removeEventListener('click', this.hideDatePanel.bind(this))
    },
    methods: {
      initData() {
        let [startDay, endDay] = this.value
        if (!(startDay instanceof dayjs)) {
          startDay = dayjs(startDay)
          if (startDay.isValid()) {
            this.chooseDay.push(startDay)
          }
        }

        if (!(endDay instanceof dayjs)) {
          endDay = dayjs(endDay)
          if (endDay.isValid()) {
            this.chooseDay.push(endDay)
          }
        }
      },
      chooseCurrentDay(day) {
        const length = this.chooseDay.length

        switch (length) {
          case 0:
          case 2:
            this.chooseDay = [day]
            break
          case 1:
            if (day > this.chooseDay[0]) {
              this.chooseDay.push(day)
            } else if (day < this.chooseDay[0]) {
              this.chooseDay.unshift(day)
            } else {
              this.chooseDay.pop()
            }
            break
        }
      },
      changeDay(type, unit) {
        if (type === 'add') {
          this.startDay = this.startDay.add(1, unit)
          this.endDay = this.endDay.add(1, unit)
        } else if (type === 'subtract') {
          this.startDay = this.startDay.subtract(1, unit)
          this.endDay = this.endDay.subtract(1, unit)
        }
      },
      changeCurrentView(view) {
        if (view >= this.startView && view <= this.endView) {
          this.currentView = view
        }
      },
      hideDatePanel(event) {
        if (this.validateChild(event.target)) return

        this.isShowDatePanel = false
      },
      validateChild(dom) {
        const parentNode = dom.parentNode

        if (!parentNode) {
          return false
        }

        if (parentNode === this.$refs.datePickerRange) {
          return true
        } else {
          return this.validateChild(parentNode)
        }
      }
    }
  }
</script>