<template>
  <div :class="customClass" @click="handleSwitch">
    <div :class="prefixClass + '__handle'"></div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-switch'

  export default {
    name: prefixClass,
    props: {
      value: {
        type: Boolean,
      },
      checked: {
        type: Boolean,
        default: false
      },
      size: {
        type: String,
        default: 'medium'
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        prefixClass,
        active: this.checked
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '

        if (this.active) {
          customClass += prefixClass + '__checked ';
        }

        if (this.disabled) {
          customClass += prefixClass + '__disabled '
        }

        switch (this.size) {
          case 'large':
            customClass += prefixClass + '__large '
            break
          case 'medium':
            customClass += prefixClass + '__medium '
            break
          case 'mini':
            customClass += prefixClass + '__mini '
            break
        }
        
        return customClass
      }
    },
    methods: {
      handleSwitch() {
        if (this.disabled) return

        this.active = !this.active
        this.$emit('input', this.active ? true : false)
      }
    }
  }
</script>