import NotificationOptions from './notification.vue';

var NotificationPlugin = {}
let instance;
let instances = [];
let seed = 1
let zIndex = 1000

NotificationPlugin.install = function (Vue, options) {
  const NotificationConstructor = Vue.extend(NotificationOptions);

  class Notify {
    constructor (option) {
      let opt = {}
      this.position = option.position || 'topRight';
      this.id =  `notification_${seed++}`

      opt = this.parseOption(option, opt)
      instance = new NotificationConstructor({
        data: opt
      })
      instance.id = this.id
      const beforeEl = instance.$mount()
      document.body.appendChild(beforeEl.$el);
      instance.visible = true
  
      this.setOffset(option)
      this.removeInstance(instance.id)
      instances.push(instance)
    }

    parseOption (option, opt) {
      if (typeof option === 'string') {
        opt = {
          title: option,
        };
      } else {
        ['title', 'description', 'type', 'position', 'duration'].forEach(property => {
          if (option[property]) {
            opt[property] = option[property]
          }
        })
      }
      opt.zIndex = zIndex++
      opt.onClose = () => {
        this.removeInstance(this.id)
      };

      return opt
    }

    setOffset (option) {
      let offset = option.offset || 0;
      instances.filter(item => item.position === this.position).forEach(item => {
        offset += item.$el.offsetHeight + 16;
      });
      offset += 16
      instance.offset = offset
    }

    removeInstance (id) {
      let index = -1;
      const len = instances.length;
      const instance = instances.filter((instance, i) => {
        if (instance.id === id) {
          index = i;
          return true;
        }
        return false;
      })[0];

      if (!instance) return;

      instances.splice(index, 1);

      if (len <= 1) return;
      
      const position = instance.position;
      const removedHeight = instance.$el.offsetHeight;
      for (let i = index; i < len - 1 ; i++) {
        if (instances[i].position === position) {
          instances[i].$el.style[instance.vertical] =
            parseInt(instances[i].$el.style[instance.vertical], 10) - removedHeight - 16 + 'px';
        }
      }
    }
  }

  Vue.prototype.$notify = (option) => {
   
    let notify = new Notify(option)

  }

  ['success', 'info', 'warning', 'error'].forEach(type => {
    Vue.prototype.$notify[type] = (option) => {
      if (typeof option === 'string') {
        option = {
          title: option
        }
      }
      
      option.type = type
      return Vue.prototype.$notify(option)
    }
  })
  
}

export default NotificationPlugin