<template>
  <div :class="customClass" @mouseenter="show" @mouseleave="hide">
    <slot></slot>
    <div :class="[prefixClass + '__dropdown']" ref="dropdown" 
      :style="{ height: visible ? dropDownHeight + 'px' : 0, }">
      <slot name="dropdown"></slot>
    </div>
  </div>
</template>

<script>
  import { prefix } from '../../utils'

  const prefixClass = prefix + '-dropdown'

  export default {
    name: prefixClass,
    props: {},
    data () {
      return {
        prefixClass,
        visible: false,
        dropDownHeight: 0,
      }
    },
    computed: {
      customClass () {
        let customClass = prefixClass + ' '
        
        return customClass
      }
    },
    mounted () {
      this.calcDropDownHeight()
    },
    methods: {
      calcDropDownHeight () {
        const $dropdownMenu = this.$children[1].$refs.menu
        const style = window.getComputedStyle($dropdownMenu)

        this.dropDownHeight = parseFloat(style.height) + parseFloat(style.marginTop) + parseFloat(style.marginBottom)
      },
      show () {
        this.calcDropDownHeight()
        this.visible = true
      },
      hide () {
        this.visible = false
      }
    }
  }
</script>